import { SPAddinContext, AccountsList, BrightcoveSettingsList, VideosList, SPUtility } from 'services/sharepoint';
import { Account } from './AccountManagement';
import { UserRole } from '../../Common';
import { find } from 'lodash';

export class SPListGateway {
    private readonly _spAddinContext: SPAddinContext;
    private readonly _accountsList: AccountsList;
    private readonly _brightcoveSettingsList: BrightcoveSettingsList;
    private readonly _videosList: VideosList;

    private readonly _errorCallback?: (message: string, err?: Error) => boolean | undefined;

    public constructor(spAddinContext: SPAddinContext, errorCallback?: (message: string, err?: Error) => boolean | undefined) {
        if (!spAddinContext) {
            throw new Error('spAddinContext must be specified.');
        }

        this._spAddinContext = spAddinContext;
        this._errorCallback = errorCallback;

        const spHostUrl: string = this._spAddinContext.SPHostUrl;
        let spAddinUrl: string | undefined = this._spAddinContext.SPAppWebUrl;

        if (spHostUrl === location.origin) {
            spAddinUrl = undefined;
        }

        this._accountsList = new AccountsList(spHostUrl, spAddinUrl, this._errorCallback);
        this._brightcoveSettingsList = new BrightcoveSettingsList(spHostUrl, spAddinUrl, this._errorCallback);
        this._videosList = new VideosList(spHostUrl, spAddinUrl, this._errorCallback);
    }

    get accountsList(): AccountsList {
        return this._accountsList;
    }

    get brightcoveSettingsList(): BrightcoveSettingsList {
        return this._brightcoveSettingsList;
    }

    get videosList(): VideosList {
        return this._videosList;
    }

    get spAddinContext(): SPAddinContext {
        return this._spAddinContext;
    }

    async validateConfiguration(currentUserEmail?: string): Promise<ConnectorValidationResult> {

        const isSettingsListCreated = await this.brightcoveSettingsList.ensureList();

        let isProxyDefined = false;
        let settingsListServerRelativeUrl: string | undefined = undefined;
        if (isSettingsListCreated) {
            isProxyDefined = await this.brightcoveSettingsList.ensureProxySettings();
        }

        // For simplicity, if the proxy isn't defined get the url of the settings list for later use.
        if (!isProxyDefined) {
            settingsListServerRelativeUrl = await this.brightcoveSettingsList.getServerRelativeUrl();
        }

        const isAccountsListCreated = await this.accountsList.ensureList();
        let areAccountsAvailable = false;
        let accountEmptyGroups = [];
        let missingGroups = [];
        let currentUserAvailableAccounts: Array<Account> = [];
        let currentUserMaxRole: UserRole = UserRole.None;

        if (isAccountsListCreated) {
            const accounts = await this.accountsList.getItems();
            areAccountsAvailable = accounts.length > 0;

            const siteGroups = await SPUtility.getSiteGroups(this._spAddinContext.SPAppWebUrl, this._spAddinContext.SPHostUrl);
            const currentUserGroups = await SPUtility.getUserGroups(this._spAddinContext.SPHostUrl, this._spAddinContext.SPAppWebUrl, currentUserEmail);

            for (const account of accounts) {
                if (!account.accountAuthorsGroupName || !account.accountViewersGroupName) {
                    accountEmptyGroups.push(account.title);
                    continue;
                }

                // If we can't find either of the defined author or viewers groups in the site groups, they're missing *cue dramatic music*
                if (!find(siteGroups, { 'Title': account.accountAuthorsGroupName }) ||
                    !find(siteGroups, { 'Title': account.accountViewersGroupName })) {
                    missingGroups.push(account.accountAuthorsGroupName);
                }

                let currentUserIsAuthor = false, currentUserIsViewer = false;

                if (find(currentUserGroups, { 'Title': account.accountAuthorsGroupName })) {
                    currentUserIsAuthor = true;
                    if (currentUserMaxRole < UserRole.Author) {
                        currentUserMaxRole = UserRole.Author;
                    }
                }

                if (find(currentUserGroups, { 'Title': account.accountViewersGroupName })) {
                    currentUserIsViewer = true;
                    if (currentUserMaxRole < UserRole.Viewer) {
                        currentUserMaxRole = UserRole.Viewer;
                    }
                }

                if (currentUserIsAuthor || currentUserIsViewer) {
                    currentUserAvailableAccounts.push(account);
                }
            }
        }

        const isVideosListCreated = await this.videosList.ensureList();

        const isValid = isSettingsListCreated &&
            isProxyDefined &&
            isAccountsListCreated &&
            isVideosListCreated &&
            accountEmptyGroups.length <= 0 &&
            missingGroups.length <= 0;

        return {
            isValid,
            isSettingsListCreated,
            isProxyDefined,
            settingsListServerRelativeUrl,
            isAccountsListCreated,
            isVideosListCreated,
            areAccountsAvailable,
            accountEmptyGroups,
            missingGroups,
            currentUserAvailableAccounts,
            currentUserMaxRole
        };
    }
}

export interface ConnectorValidationResult {
    isValid: boolean;
    isSettingsListCreated: boolean;
    isProxyDefined: boolean;
    settingsListServerRelativeUrl?: string;
    isAccountsListCreated: boolean;
    isVideosListCreated: boolean;
    areAccountsAvailable: boolean;
    accountEmptyGroups: Array<string>;
    missingGroups: Array<string>;
    currentUserAvailableAccounts: Array<Account>;
    currentUserMaxRole: UserRole;
}


