import * as React from 'react';
import { HashRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import { LoadingProgress } from './components/common';
import { MiniExperiences } from './components/mini-experiences';
import { MiniPlaylists } from 'components/mini-playlists';
import { MiniVideos } from './components/mini-videos';

import { Account, SPListGateway } from './services/sharepoint';
import { BrightcoveApiGateway } from './services/brightcove';

import './Mini.css';

export class Mini extends React.Component<MiniProps, MiniState> {
    constructor(props: MiniProps, context?: any) {
        super(props, context);

        this.state = {
            isLoading: true,
            availableAccounts: [],
        };
    }

    async componentDidMount() {
        const { spListGateway } = this.props;

        this.setState({
            isLoading: true,
        });

        const availableAccounts = await spListGateway.accountsList.getAvailableAccounts();

        this.setState({
            isLoading: false,
            availableAccounts: availableAccounts,
        });
    }

    render() {
        const { bcApiGateway } = this.props;
        const { isLoading, availableAccounts } = this.state;

        if (isLoading) {
            return (
                <div className="mini">
                    <LoadingProgress loadingMessage="Loading Brightcove Mini..." />
                </div>
            );
        }

        return (
            <Router>
                <div className="mini">
                    <Switch>
                        <Route path="/mini/videos" exact={true}>
                            <MiniVideos availableAccounts={availableAccounts} bcApiGateway={bcApiGateway} />
                        </Route>
                        <Route path="/mini/playlists" exact={true}>
                            <MiniPlaylists availableAccounts={availableAccounts} bcApiGateway={bcApiGateway} />
                        </Route>
                        <Route path="/mini/experiences" exact={true}>
                            <MiniExperiences availableAccounts={availableAccounts} bcApiGateway={bcApiGateway} />
                        </Route>
                        <Redirect to="/" />
                    </Switch>
                </div>
            </Router>
        );
    }
}

interface MiniProps {
    spListGateway: SPListGateway;
    bcApiGateway: BrightcoveApiGateway;
    raiseError: (message: string, err?: Error) => boolean;
}

interface MiniState {
    isLoading: boolean;
    availableAccounts: Array<Account>;
}



