import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';
import { HashRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import { Spinner } from './components/common';
import autobind from 'autobind-decorator';

import { SPAddinContext, SPListGateway, SPUtility, ConnectorValidationResult } from './services/sharepoint';
import { BrightcoveApiGateway } from './services/brightcove';

import { Home } from './components/home';
import { AccountManagement, AccountView } from './components/account-management';
import { AddVideos } from './components/add-videos';
import { ManageVideos, EditVideo } from './components/manage-videos';
import { ManagePlaylists, EditPlaylist } from './components/manage-playlists';
import { ManageExperiences, EditExperience } from './components/manage-experiences';
import { LoadingProgress } from './components/common';
import { EditVideoTextTracksStory, VideoEmbedStory } from './stories';

import { ConnectorContext, UserRole } from './Common';
import './Manage.css';

const RouteButton = ({ label, to, activeOnlyWhenExact }: { label: string, to: string, activeOnlyWhenExact?: boolean }) => (
    <Route
        path={to}
        exact={activeOnlyWhenExact}
        children={({ match }) => (
            <Button bsSize="large" block={true} href={'#' + to} bsStyle={match ? 'primary' : 'default'}>
                {label}
            </Button>
        )}
    />
);

export class Manage extends React.Component<ManageProps, ManageState> {
    async componentDidMount() {
        const { spAddinContext, spListGateway } = this.props;

        this.setState({
            isLoading: true,
        });

        const validationResult = await spListGateway.validateConfiguration(process.env.REACT_APP_BC_Override_Current_User);

        const webId = await SPUtility.getWebId(spAddinContext.SPAppWebUrl);

        const connectorContext: ConnectorContext = {
            addinWebId: webId,
            availableAccounts: validationResult.currentUserAvailableAccounts,
            currentUserRole: validationResult.currentUserMaxRole
        };

        this.setState({
            isLoading: false,
            validationResult: validationResult,
            connectorContext,
            showInstallationModal: !validationResult.isValid,
            isPerformingInstallationAction: false,
        });
    }

    @autobind
    async createSettingsList() {
        const { spListGateway } = this.props;

        this.setState({
            isPerformingInstallationAction: true,
            installationActionMessage: 'Creating settings list...'
        });
        await spListGateway.brightcoveSettingsList.ensureList(true);
        location.reload(true);
    }

    @autobind
    async createAccountsList() {
        const { spListGateway } = this.props;

        this.setState({
            isPerformingInstallationAction: true,
            installationActionMessage: 'Creating accounts list...'
        });
        await spListGateway.accountsList.ensureList(true);
        location.reload(true);
    }

    @autobind
    async createVideosList() {
        const { spListGateway } = this.props;

        this.setState({
            isPerformingInstallationAction: true,
            installationActionMessage: 'Creating videos list...'
        });
        await spListGateway.videosList.ensureList(true);
        location.reload(true);
    }

    @autobind
    hideInstallationModel() {
        this.setState({
            showInstallationModal: false,
        });
    }

    render() {
        if (!this.state || this.state.isLoading) {
            return (
                <LoadingProgress />
            );
        }

        const {
            spAddinContext,
            spListGateway,
            bcApiGateway,
        } = this.props;

        const {
            connectorContext,
            validationResult,
            showInstallationModal,
            isPerformingInstallationAction,
            installationActionMessage
        } = this.state;

        const settingsListUrl = `${spAddinContext.SPHostUrl}${validationResult.settingsListServerRelativeUrl}`;
        const showAccountManagement = connectorContext && connectorContext.currentUserRole && connectorContext.currentUserRole === UserRole.Author;

        return (
            <Router>
                <div className="App">
                    <div className="container body-content">
                        <div className="row">
                            <div className="col-md-4">
                                <nav className="leftNav">
                                    <RouteButton to="/" label="Home" activeOnlyWhenExact={true} />
                                    {showAccountManagement &&
                                        <RouteButton to="/account-management" label="Account Management" />
                                    }
                                    <RouteButton to="/add-videos" label="Add Videos" />
                                    <RouteButton to="/manage-videos" label="Manage Videos" />
                                    <RouteButton to="/manage-playlists" label="Manage Playlists" />
                                    <RouteButton to="/manage-experiences" label="Manage Experiences" />
                                </nav>
                            </div>
                            <div className="col-md-8">
                                {this.state &&
                                    <Switch>
                                        <Route path="/" exact={true} component={Home} />
                                        <Route
                                            path="/account-management"
                                            exact={true}
                                            render={props => (
                                                <AccountManagement connectorContext={connectorContext} bcApiGateway={bcApiGateway} spListGateway={spListGateway} />
                                            )}
                                        />
                                        <Route
                                            path="/account-management/view/:accountId"
                                            render={props => (
                                                <AccountView connectorContext={connectorContext} bcApiGateway={bcApiGateway} spListGateway={spListGateway} accountId={props.match.params.accountId} />
                                            )}
                                        />
                                        <Route
                                            path="/add-videos"
                                            render={props => (
                                                <AddVideos
                                                    connectorContext={connectorContext}
                                                    bcApiGateway={bcApiGateway}
                                                    spListGateway={spListGateway}
                                                />
                                            )}
                                        />
                                        <Route
                                            path="/manage-videos"
                                            exact={true}
                                            render={props => (
                                                <ManageVideos connectorContext={connectorContext} bcApiGateway={bcApiGateway} spListGateway={spListGateway} />
                                            )}
                                        />
                                        <Route
                                            path="/manage-videos/edit/:accountId/:videoLocation(SharePoint|Brightcove)/:videoId"
                                            render={props => (
                                                <EditVideo connectorContext={connectorContext} bcApiGateway={bcApiGateway} spListGateway={spListGateway} accountId={props.match.params.accountId} videoLocation={props.match.params.videoLocation} videoId={props.match.params.videoId} />
                                            )}
                                        />
                                        <Route
                                            path="/manage-playlists"
                                            exact={true}
                                            render={props => (
                                                <ManagePlaylists connectorContext={connectorContext} bcApiGateway={bcApiGateway} spListGateway={spListGateway} />
                                            )}
                                        />
                                        <Route
                                            path="/manage-playlists/new/:accountId/playlist"
                                            render={props => (
                                                <EditPlaylist bcApiGateway={bcApiGateway} spListGateway={spListGateway} accountId={props.match.params.accountId} />
                                            )}
                                        />
                                        <Route
                                            path="/manage-playlists/edit/:accountId/playlist/:playlistId"
                                            render={props => (
                                                <EditPlaylist bcApiGateway={bcApiGateway} spListGateway={spListGateway} accountId={props.match.params.accountId} playlistId={props.match.params.playlistId} />
                                            )}
                                        />
                                        <Route
                                            path="/manage-experiences"
                                            exact={true}
                                            render={props => (
                                                <ManageExperiences connectorContext={connectorContext} bcApiGateway={bcApiGateway} spListGateway={spListGateway} />
                                            )}
                                        />
                                        <Route
                                            path="/manage-experiences/edit/:accountId/experience/:experienceId"
                                            render={props => (
                                                <EditExperience connectorContext={connectorContext} bcApiGateway={bcApiGateway} spListGateway={spListGateway} accountId={props.match.params.accountId} experienceId={props.match.params.experienceId} />
                                            )}
                                        />
                                        <Route
                                            path="/_stories/TextTrack"
                                            render={props => (
                                                <EditVideoTextTracksStory />
                                            )}
                                        />
                                        <Route
                                            path="/_stories/VideoEmbed"
                                            render={props => (
                                                <VideoEmbedStory bcApiGateway={bcApiGateway} />
                                            )}
                                        />
                                        <Redirect to="/" />
                                    </Switch>
                                }
                            </div>
                        </div>
                        <hr />
                        <footer>
                            <p className="copyright">© 2016 - Brightcove Video Management</p>
                            <p className="version">v3.0.0</p>
                        </footer>
                    </div>
                    <Modal className="installation-modal" show={showInstallationModal} onHide={this.hideInstallationModel} bsSize="large">
                        <Modal.Header closeButton={true}>
                            <Modal.Title>Brightcove Video Connector for SharePoint Installation</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            {isPerformingInstallationAction &&
                                <Spinner loadingText={installationActionMessage || 'Configuring...'} />
                            }
                            {!isPerformingInstallationAction &&
                                <>
                                    <h4>Welcome to the Brightcove Video Connector for SharePoint!</h4>
                                    <p>We've detected a few installation steps that need to be completed:</p>
                                    <ul>
                                        {!validationResult.isSettingsListCreated &&
                                            <li>
                                                <span><strong>Missing Settings list</strong></span>
                                                <div>The Settings list is needed to specify Brightcove Proxy settings. <Button bsStyle="link" onClick={this.createSettingsList}>Click here to create the Settings List.</Button></div>
                                                <br />
                                            </li>
                                        }
                                        {validationResult.isSettingsListCreated && !validationResult.isProxyDefined &&
                                            <li>
                                                <span><strong>Missing/Empty Proxy settings.</strong></span>
                                                <div>A list item titled 'Proxy' must exist and contain Brightcove proxy settings. <a href={settingsListUrl} target="_blank">Click here to open the Settings list</a></div>
                                                <br />
                                            </li>
                                        }
                                        {validationResult.isSettingsListCreated && !validationResult.isAccountsListCreated &&
                                            <li>
                                                <span><strong>Missing Accounts list</strong></span>
                                                <div>The Accounts list is required to specify Brightcove account information. <Button bsStyle="link" onClick={this.createAccountsList}>Click here to create the Accounts List.</Button></div>
                                                <br />
                                            </li>
                                        }
                                        {validationResult.isSettingsListCreated && !validationResult.isVideosListCreated &&
                                            <li>
                                                <span><strong>Missing Videos list</strong></span>
                                                <div>The Videos list is required to save video metadata for easy video retrieval. <Button bsStyle="link" onClick={this.createVideosList}>Click here to create the Videos List.</Button></div>
                                                <br />
                                            </li>
                                        }
                                        {validationResult.isSettingsListCreated &&
                                            validationResult.isAccountsListCreated &&
                                            validationResult.areAccountsAvailable &&
                                            validationResult.accountEmptyGroups.length > 0 &&
                                            <li>
                                                <span><strong>Groups not defined in account</strong></span>
                                                <div>The following accounts do not have groups defined:</div>
                                                {validationResult.accountEmptyGroups.map((accountName, ix) =>
                                                    <div key={ix}>{accountName}</div>
                                                )}
                                                <br />
                                                <p>Please correct this by editing the account settings</p>
                                                <br />
                                            </li>
                                        }

                                        {validationResult.isSettingsListCreated &&
                                            validationResult.isAccountsListCreated &&
                                            validationResult.areAccountsAvailable &&
                                            validationResult.missingGroups.length > 0 &&
                                            <li>
                                                <span><strong>Missing Groups</strong></span>
                                                <div>The following groups are specified within account settings but have not been created:</div>
                                                {validationResult.missingGroups.map((missingGroup, ix) =>
                                                    <div key={ix}>{missingGroup}</div>
                                                )}
                                                <br />
                                            </li>
                                        }
                                    </ul>
                                    <p><i>For more information or assistance, please visit the Brightcove Video Connector documentation <a href="https://support.brightcove.com/getting-started-brightcove-video-connect-sharepoint"> by clicking this link</a></i></p>
                                </>
                            }
                        </Modal.Body>
                        <Modal.Footer />
                    </Modal>
                </div>
            </Router>
        );
    }
}

interface ManageProps {
    spAddinContext: SPAddinContext;
    spListGateway: SPListGateway;
    bcApiGateway: BrightcoveApiGateway;
    raiseError: (message: string, err?: Error) => boolean;
}

interface ManageState {
    isLoading: boolean;
    validationResult: ConnectorValidationResult;
    connectorContext: ConnectorContext;

    showInstallationModal: boolean;
    isPerformingInstallationAction: boolean;
    installationActionMessage?: string;
}

export default Manage;



