import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import { VideoEmbed } from '../embeds';

import { SearchTarget, SortField, SortOrder, VideoSearchParameters, getDefaultVideoSearchParameters } from '.';
import { AccountPicker, Spinner } from '../common';
import autobind from 'autobind-decorator';
import { filter, orderBy, isEqual } from 'lodash';

import { BrightcoveApiGateway, BCGetVideosParams } from '../../services/brightcove';
import { SPListGateway, Account, Video } from '../../services/sharepoint';
import { ConnectorContext } from '../../Common';

import { MVideo, mapSPVideoToMVideo, mapBCVideoToMVideo } from './mVideo';
import { VideoSearchControls } from './VideoSearchControls';

import './ManageVideos.css';

export class ManageVideos extends React.Component<ManageVideosProps, ManageVideosState> {
    constructor(props: ManageVideosProps, context?: any) {
        super(props, context);

        this.state = {
            searchParameters: getDefaultVideoSearchParameters(),
            isLoadingVideos: false,
            showEmbedModal: false
        };
    }

    @autobind
    async componentDidMount() {
        this.searchForVideos(this.state.searchParameters);
    }

    @autobind
    async componentDidUpdate(prevProps: ManageVideosProps, prevState: ManageVideosState) {
        const { selectedAccount, searchParameters } = this.state;

        if (selectedAccount !== prevState.selectedAccount) {
            await this.searchForVideos(searchParameters);
        }
    }

    @autobind
    async handleAccountChange(selectedAccount: Account) {
        this.setState({
            selectedAccount
        });
    }

    @autobind
    async searchForVideos(searchParams: Partial<VideoSearchParameters>): Promise<void> {
        const { selectedAccount } = this.state;

        if (!selectedAccount || !selectedAccount.accountId) {
            return;
        }

        this.setState({
            isLoadingVideos: true,
        });

        let videos: Array<MVideo>;
        switch (searchParams.searchTarget) {
            case SearchTarget.SharePoint:
                videos = await this.searchForSharePointVideos(selectedAccount, searchParams);
                break;
            case SearchTarget.VideoCloud:
                videos = await this.searchForVideoCloudVideos(selectedAccount, searchParams);
                break;
            default:
                return;
        }

        this.setState({
            isLoadingVideos: false,
            videos
        });
    }

    @autobind
    async searchForSharePointVideos(selectedAccount: Account, searchParams: Partial<VideoSearchParameters>): Promise<Array<MVideo>> {
        const { spListGateway } = this.props;

        const spVideos = await spListGateway.videosList.getVideosByAccountName(selectedAccount.title);

        let result: Array<MVideo> = [];
        for (const spVideo of spVideos) {
            result.push(mapSPVideoToMVideo(spVideo));
        }

        if (searchParams.searchTerms) {
            const terms = searchParams.searchTerms.toLowerCase();
            result = filter(result, (r) => r.name && r.name.toLowerCase().indexOf(terms) > -1);
        }

        const sortDirection = searchParams.sortOrder === SortOrder.Ascending ? 'asc' : 'desc';

        // Perform a client-side search.
        switch (searchParams.sortField) {
            case SortField.Name:
                result = orderBy(result, ['name'], [sortDirection]);
                break;
            case SortField.Relevance:
                result = orderBy(result, ['sharepointId'], [sortDirection]);
                break;
            case SortField.CreationDate:
                result = orderBy(result, ['creationDate'], [sortDirection]);
                break;
            case SortField.StartDate:
                result = orderBy(result, ['startDate'], [sortDirection]);
                break;
            default:
                // Do nothing.
                break;
        }

        this.setState({
            spVideos
        });

        return result;
    }

    @autobind
    async searchForVideoCloudVideos(selectedAccount: Account, searchParams: Partial<VideoSearchParameters>): Promise<Array<MVideo>> {

        const { connectorContext, bcApiGateway } = this.props;
        const videosApi = bcApiGateway.videosApi;

        // Map search params to 'q'
        let bcParams: BCGetVideosParams = {
            limit: searchParams.searchLimit || 45,
            sort: searchParams.sortOrder === SortOrder.Descending ? '-' : '',
            q: searchParams.searchTerms ? `+"${searchParams.searchTerms}"` : ''
        };

        // Remove videos that are tagged with the addin web id.
        if (connectorContext.addinWebId) {
            bcParams.q += `-tags:"bcsp-${connectorContext.addinWebId.replace(/-/g, '')}-1"`;
        }

        switch (searchParams.sortField) {
            case SortField.Name:
                bcParams.sort += 'name';
                break;
            case SortField.Relevance:
                bcParams.sort += 'plays_total';
                break;
            case SortField.CreationDate:
                bcParams.sort += 'created_at';
                break;
            case SortField.StartDate:
                bcParams.sort += 'schedule_starts_at';
                break;
            default:
                // Do nothing.
                break;
        }

        const videos = await videosApi.getVideos(selectedAccount.accountId, bcParams);

        let result: Array<MVideo> = [];
        for (const video of videos) {
            result.push(mapBCVideoToMVideo(video));
        }
        return result;
    }

    @autobind
    handleSearchParametersChanged(searchParameters: Partial<VideoSearchParameters>, fieldChanged: string) {
        this.setState({
            searchParameters
        });

        if (fieldChanged !== 'searchTerms') {
            this.searchForVideos(searchParameters);
        }
    }

    @autobind
    showEmbedModal(videoId: string) {
        this.setState({
            showEmbedModal: true,
            embedVideoId: videoId
        });
    }

    @autobind
    hideEmbedModal() {
        this.setState({
            showEmbedModal: false
        });
    }

    @autobind
    renderVideoCell(cell: any, video: MVideo, extraData: { selectedAccount: Account }): React.ReactElement<any> {
        const { selectedAccount } = extraData;
        const { searchTarget } = this.state.searchParameters;

        let videoLocation = 'Brightcove';
        if (searchTarget === SearchTarget.SharePoint) {
            videoLocation = 'SharePoint';
        }

        const editVideoUrl = `#/manage-videos/edit/${selectedAccount.accountId}/${videoLocation}/${video.videoId}`;
        const tagsToDisplay = filter(video.tags, (tag) => !tag.startsWith('bcsp-'));

        return (
            <div className="result">
                <img className="resultImage col-md-2" src={video.thumbnailUrl} />
                <div className="col-md-7">
                    <div><a href={editVideoUrl}>{video.name}</a></div>
                    <div>
                        {tagsToDisplay.map((tag, i) =>
                            <Button key={i} bsSize="xs" bsStyle="success">{tag}</Button>
                        )}
                    </div>
                </div>
                <div className="col-md-3 text-center">
                    <div>
                        <a href={editVideoUrl}>Edit Video</a>
                    </div>
                    <div className="video-id">Video Id: {video.videoId}</div>
                    <div>
                        <Button
                            bsStyle="success"
                            bsSize="small"
                            style={{ width: '90px' }}
                            onClick={() => this.showEmbedModal(video.videoId)}
                        >
                            Embed
                        </Button>
                    </div>
                </div>
            </div>
        );
    }

    render() {

        const {
            bcApiGateway,
            connectorContext
        } = this.props;

        const {
            selectedAccount,
            isLoadingVideos,
            searchParameters,
            videos,
            showEmbedModal,
            embedVideoId
        } = this.state;

        const labelClass = 'col-md-3 control-label';
        const controlClass = 'col-md-9';

        return (
            <>
                <ol className="breadcrumb">
                    <li><a href="#/">Brightcove Management</a></li>
                    <li className="active">Manage Videos</li>
                </ol>
                <div className="search">
                    <div className="form-group">
                        <label>Select Account:</label>
                        <div>
                            <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccount={selectedAccount} onChange={this.handleAccountChange} />
                        </div>
                    </div>
                </div>
                <div className="search form-horizontal">
                    {selectedAccount &&
                        <>
                            <VideoSearchControls labelClass={labelClass} controlClass={controlClass} searchParameters={searchParameters} onSearchParametersChanged={this.handleSearchParametersChanged} onSearchForVideos={this.searchForVideos} />
                            {isLoadingVideos &&
                                <Spinner loadingText="Loading Videos..." />
                            }
                            {!isLoadingVideos && videos &&
                                <BootstrapTable data={videos} striped={true} bordered={false} pagination={videos.length > 10} options={{ paginationPosition: 'bottom' }}>
                                    <TableHeaderColumn dataField="videoId" isKey={true} dataFormat={this.renderVideoCell} formatExtraData={{ selectedAccount }}>Results</TableHeaderColumn>
                                </BootstrapTable>
                            }
                            {embedVideoId &&
                                <Modal className="embed" show={showEmbedModal} onHide={this.hideEmbedModal} bsSize="large">
                                    <Modal.Header closeButton={true}>
                                        <Modal.Title>Embed Video</Modal.Title>
                                    </Modal.Header>
                                    <Modal.Body>
                                        <VideoEmbed
                                            bcApiGateway={bcApiGateway}
                                            accountId={selectedAccount.accountId}
                                            videoId={embedVideoId}
                                        />
                                    </Modal.Body>
                                    <Modal.Footer />
                                </Modal>
                            }
                        </>
                    }
                </div>
            </>
        );
    }
}

interface ManageVideosProps {
    connectorContext: ConnectorContext;
    bcApiGateway: BrightcoveApiGateway;
    spListGateway: SPListGateway;
}

interface ManageVideosState {
    searchParameters: Partial<VideoSearchParameters>;

    isLoadingVideos: boolean;
    spVideos?: Array<Video>;
    videos?: Array<MVideo>;
    selectedAccount?: Account;

    showEmbedModal: boolean;
    embedVideoId?: string;
}


