import * as React from 'react';
import autobind from 'autobind-decorator';

import { VideoEmbed } from '../components/embeds';
import { BCPlayer, BrightcoveApiGateway } from '../services/brightcove';

export class VideoEmbedStory extends React.Component<VideoEmbedProps, VideoEmbedState> {
    constructor(props: VideoEmbedProps, context?: any) {
        super(props, context);

        this.state = {
        };
    }

    render() {
        const accountId: string = '5718741448001';
        const videoId: string = '5742275941001';
        const { bcApiGateway } = this.props;

        return (
            <VideoEmbed
                bcApiGateway={bcApiGateway}
                accountId={accountId}
                videoId={videoId}
            />
        );
    }
}

interface VideoEmbedProps {
    bcApiGateway: BrightcoveApiGateway;
}

interface VideoEmbedState {
    selectedPlayer?: BCPlayer;
}



