import * as React from 'react';
import { Button } from 'react-bootstrap';
import autobind from 'autobind-decorator';

import { SearchTarget, SortField, SortOrder, VideoSearchParameters } from '.';

export class VideoSearchControls extends React.Component<VideoSearchControlsProps> {

    @autobind
    async handleSearchTermsChange(e: React.ChangeEvent<HTMLInputElement>) {
        const { searchParameters, onSearchParametersChanged } = this.props;
        searchParameters.searchTerms = e.target.value;

        onSearchParametersChanged(searchParameters, 'searchTerms');
    }

    @autobind
    handleSearchTargetChanged(newSearchTarget: SearchTarget) {
        const { searchParameters, onSearchParametersChanged } = this.props;
        searchParameters.searchTarget = newSearchTarget;

        onSearchParametersChanged(searchParameters);
    }

    @autobind
    async handleSortFieldChanged(newSortField: SortField) {
        const { searchParameters, onSearchParametersChanged } = this.props;
        searchParameters.sortField = newSortField;

        onSearchParametersChanged(searchParameters);
    }

    @autobind
    async handleSortOrderChanged(newSortOrder: SortOrder) {
        const { searchParameters, onSearchParametersChanged } = this.props;
        searchParameters.sortOrder = newSortOrder;

        onSearchParametersChanged(searchParameters);
    }

    render() {
        const { searchParameters, onSearchForVideos, showSearchTarget, labelClass, controlClass, formControlClass } = this.props;
        const { searchTerms, searchTarget, sortField, sortOrder } = searchParameters;

        const shouldShowSearchTarget = showSearchTarget === undefined || showSearchTarget === true;

        return (
            <>
                {shouldShowSearchTarget &&
                    <div className="row">
                        <div className="form-group col-md-5">
                            <label>Search</label>
                            <div>
                                <div className="input-group">
                                    <input
                                        type="search"
                                        placeholder="Search"
                                        className={`form-control ${formControlClass}`}
                                        value={searchTerms}
                                        onKeyPress={(e) => e.key === 'Enter' && onSearchForVideos(searchParameters)}
                                        onChange={this.handleSearchTermsChange}
                                    />
                                    <div className="input-group-btn">
                                        <Button onClick={() => onSearchForVideos(searchParameters)}><i className="glyphicon glyphicon-search" /></Button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-1" />
                        <div className={`form-group col-md-6`}>
                            <label>Target</label>
                            <div>
                                <div className="input-group">
                                    <label className="radio-inline">
                                        <input type="radio" checked={searchTarget === SearchTarget.SharePoint} onChange={() => this.handleSearchTargetChanged(SearchTarget.SharePoint)} />SharePoint only
                                    </label>
                                    <label className="radio-inline">
                                        <input type="radio" checked={searchTarget === SearchTarget.VideoCloud} onChange={() => this.handleSearchTargetChanged(SearchTarget.VideoCloud)} />Video Cloud only
                                    </label>
                                </div>
                            </div>
                        </div>
                    </div>
                }
                {!shouldShowSearchTarget &&
                    <div className="form-group">
                        <label className={labelClass}>Available Videos:</label>
                        <div className={controlClass}>
                            <div className="input-group">
                                <input
                                    type="search"
                                    placeholder="Video Filter"
                                    className={`form-control ${formControlClass}`}
                                    value={searchTerms}
                                    onKeyPress={(e) => e.key === 'Enter' && onSearchForVideos(searchParameters)}
                                    onChange={this.handleSearchTermsChange}
                                />
                                <div className="input-group-btn">
                                    <Button bsSize="small" onClick={() => onSearchForVideos(searchParameters)}><i className="glyphicon glyphicon-search" /></Button>
                                </div>
                            </div>
                        </div>
                    </div>
                }
                <div className="form-group">
                    <label className={labelClass}>Sort results by: </label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <label className="radio-inline">
                                <input type="radio" checked={sortField === SortField.Name} onChange={() => this.handleSortFieldChanged(SortField.Name)} />display name
                            </label>
                            <label className="radio-inline">
                                <input type="radio" checked={sortField === SortField.Relevance} onChange={() => this.handleSortFieldChanged(SortField.Relevance)} />relevance
                            </label>
                            <label className="radio-inline">
                                <input type="radio" checked={sortField === SortField.CreationDate} onChange={() => this.handleSortFieldChanged(SortField.CreationDate)} />creation date
                            </label>
                            <label className="radio-inline">
                                <input type="radio" checked={sortField === SortField.StartDate} onChange={() => this.handleSortFieldChanged(SortField.StartDate)} />start date
                            </label>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Order: </label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <label className="radio-inline">
                                <input type="radio" checked={sortOrder === SortOrder.Ascending} onChange={() => this.handleSortOrderChanged(SortOrder.Ascending)} />ascending
                            </label>
                            <label className="radio-inline">
                                <input type="radio" checked={sortOrder === SortOrder.Descending} onChange={() => this.handleSortOrderChanged(SortOrder.Descending)} />descending
                            </label>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

export interface VideoSearchControlsProps {
    showSearchTarget?: boolean;
    labelClass?: string;
    controlClass?: string;
    formControlClass?: string;
    searchParameters: Partial<VideoSearchParameters>;
    onSearchParametersChanged: (searchParameters: Partial<VideoSearchParameters>, fieldUpdated?: string) => void;
    onSearchForVideos: (searchParameters: Partial<VideoSearchParameters>) => void;
}


