import {
    BCAccessTokenService, BCGetExperiencesResponse, BCExperience,
} from './index';
import { ExperiencesEndpointUrl } from './Endpoints';

/**
 * See:
 * Appendix D of Tim's Requirements Document
 */

export class ExperiencesApi {
    private readonly _accessTokenService: BCAccessTokenService;

    constructor(accessTokenService: BCAccessTokenService) {
        this._accessTokenService = accessTokenService;
    }

    public async getExperiences(accountId: string): Promise<BCGetExperiencesResponse> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${ExperiencesEndpointUrl}/${accountId}/experiences`;

        let response = await fetch(url, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }

    public async getExperienceById(accountId: string, experienceId: string): Promise<BCExperience> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${ExperiencesEndpointUrl}/${accountId}/experiences/${experienceId}`;

        let response = await fetch(url, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }

    public async publishExperience(accountId: string, experienceId: string): Promise<BCExperience> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${ExperiencesEndpointUrl}/${accountId}/experiences/${experienceId}/actions/publish`;

        let response = await fetch(url, {
            method: 'POST',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }
}


