import {
    BCAccessTokenService,
    BCPlaylist,
    BCVideo,
    BCGetPlaylistsParams,
    BCPlaylistType,
} from './index';
import { CmsEndpointUrl } from './Endpoints';
import { getQueryString } from '../../services/Utils';
import { pick } from 'lodash';

/**
 * See https://brightcovelearning.github.io/Brightcove-API-References/cms-api/v1/doc/index.html#api-playlistGroup
 */
export class PlaylistsApi {
    private readonly _accessTokenService: BCAccessTokenService;

    constructor(accessTokenService: BCAccessTokenService) {
        this._accessTokenService = accessTokenService;
    }

    /**
     * https://brightcovelearning.github.io/Brightcove-API-References/cms-api/v1/doc/index.html#api-playlistGroup-Create_Playlist
     * @param accountId 
     */
    public async createPlaylist(accountId: string, playlist: BCPlaylist): Promise<BCPlaylist> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/playlists`;

        let playlistRequest;
        if (playlist.type === BCPlaylistType.Manual) {
            playlistRequest = pick(playlist, ['name', 'description', 'favorite', 'reference_id', 'type', 'video_ids']);
        } else {
            playlistRequest = pick(playlist, ['name', 'description', 'favorite', 'reference_id', 'type', 'search', 'limit']);
        }

        let response = await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            },
            body: JSON.stringify(playlistRequest)
        });

        return await response.json();
    }

    public async getPlaylists(accountId: string, options?: BCGetPlaylistsParams): Promise<Array<BCPlaylist>> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/playlists`;
        if (options) {
            url += '?' + getQueryString(options);
        }

        let response = await fetch(url, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }

    public async getPlaylistById(accountId: string, playlistId: string): Promise<BCPlaylist> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/playlists/${playlistId}`;

        let response = await fetch(url, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }

    /**
     * https://brightcovelearning.github.io/Brightcove-API-References/cms-api/v1/doc/index.html#api-playlistGroup-Get_Videos_in_Playlist
     * @param accountId Video Cloud account Id.
     * @param playlistId Video Cloud playlist Id.
     */
    public async getVideosInPlaylist(accountId: string, playlistId: string): Promise<Array<BCVideo>> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/playlists/${playlistId}/videos`;

        let response = await fetch(url, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }

    public async updatePlaylist(accountId: string, playlistId: string, playlist: BCPlaylist): Promise<BCPlaylist> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/playlists/${playlistId}`;

        // Remove unused properties, such as 'updated_at' and 'created_at'.
        // Also remove fields not appropriate for the type.
        // otherwise a 422 response happens.
        let playlistRequest;
        if (playlist.type === BCPlaylistType.Manual) {
            playlistRequest = pick(playlist, ['name', 'description', 'favorite', 'reference_id', 'type', 'video_ids']);
        } else {
            playlistRequest = pick(playlist, ['name', 'description', 'favorite', 'reference_id', 'type', 'search', 'limit']);
        }

        let response = await fetch(url, {
            method: 'PATCH',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            },
            body: JSON.stringify(playlistRequest)
        });

        return await response.json();
    }

    public async deletePlaylist(accountId: string, playlistId: string): Promise<void> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/playlists/${playlistId}`;

        await fetch(url, {
            method: 'DELETE',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });
    }
}


