import * as React from 'react';
import { Button } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn, SelectRow } from 'react-bootstrap-table';
import autobind from 'autobind-decorator';
import { AccountPicker, Spinner } from '../common';
import { ExperienceEmbed } from '../embeds';

import { Account } from '../../services/sharepoint';
import { BrightcoveApiGateway, BCExperience } from '../../services/brightcove';

import { filter } from 'lodash';

export class MiniExperiences extends React.Component<MiniExperiencesProps, MiniExperiencesState> {
    constructor(props: MiniExperiencesProps, context?: any) {
        super(props, context);

        this.state = {
            experiencesFilter: '',
            isLoadingExperiences: true,
            allExperiences: [],
            filteredExperiences: []
        };
    }
    @autobind
    async handleAccountChange(selectedAccount: Account) {
        this.setState({
            isLoadingExperiences: true,
            selectedAccount,
            experiencesFilter: ''
        });

        const experiencesApi = this.props.bcApiGateway.experiencesApi;
        const experiencesResponse = await experiencesApi.getExperiences(selectedAccount.accountId);

        this.setState({
            isLoadingExperiences: false,
            allExperiences: experiencesResponse.items,
            filteredExperiences: experiencesResponse.items
        });
    }

    @autobind
    handleExperiencesFilterChange(experiencesFilter: string) {
        this.setState({
            experiencesFilter
        });

        const { allExperiences } = this.state;
        let filteredExperiences = allExperiences;
        if (experiencesFilter) {
            const strFilter = experiencesFilter.toLowerCase();
            filteredExperiences = filter(filteredExperiences, (ex) => ex.name && ex.name.toLowerCase().indexOf(strFilter) > -1);
        }

        this.setState({
            filteredExperiences
        });
    }

    @autobind
    experienceSelected(experience: BCExperience, isSelected: boolean, e: any) {
        let selectedExperience: BCExperience | undefined;
        if (isSelected) {
            selectedExperience = experience;
        }
        this.setState({
            selectedExperience
        });
    }

    @autobind
    renderExperienceNameCell(cell: any, experience: BCExperience, extraData: { selectedAccount: Account }) {
        return (
            <>
                <a href="#">{experience.name}</a>
                <br />
                <div className="small">{experience.template}</div>
                <div className="small">{experience.id}</div>
            </>
        );
    }

    render() {
        const { availableAccounts } = this.props;
        const {
            selectedAccount,
            experiencesFilter,
            isLoadingExperiences,
            filteredExperiences,
            selectedExperience
        } = this.state;

        const labelClass = 'col-md-3 control-label small';
        const controlClass = 'col-md-9 word-wrap';

        const selectRow: SelectRow = {
            mode: 'radio',
            clickToSelect: true,
            className: 'info',
            onSelect: this.experienceSelected
        };

        return (
            <div className="form-horizontal">
                <div className="form-group">
                    <label className={labelClass}>Brightcove Account:</label>
                    <div className={controlClass}>
                        <AccountPicker className="input-sm" availableAccounts={availableAccounts} selectedAccount={selectedAccount} onChange={this.handleAccountChange} />
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Experiences Filter:</label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <input
                                type="search"
                                placeholder="Search"
                                className="form-control input-sm"
                                value={experiencesFilter}
                                onChange={(e) => this.handleExperiencesFilterChange(e.target.value)}
                            />
                            <div className="input-group-btn searchButton">
                                <Button bsSize="small"><i className="glyphicon glyphicon-search" /></Button>
                            </div>
                        </div>
                    </div>
                </div>
                {isLoadingExperiences && selectedAccount &&
                    <Spinner loadingText="Loading Experiences..." />
                }
                {!isLoadingExperiences && selectedAccount &&
                    <>
                        <div className="well" style={{ height: '250px', overflow: 'auto' }} >
                            <BootstrapTable data={filteredExperiences} striped={true} bordered={false} selectRow={selectRow}>
                                <TableHeaderColumn dataField="name" dataFormat={this.renderExperienceNameCell} formatExtraData={{ selectedAccount }} dataSort={true}>Name</TableHeaderColumn>
                                <TableHeaderColumn dataField="description" dataSort={true}>Short Description</TableHeaderColumn>
                                <TableHeaderColumn dataField="id" dataSort={true} isKey={true} hidden={true}>ID</TableHeaderColumn>
                            </BootstrapTable>
                            <hr />
                        </div>
                        <ExperienceEmbed
                            accountId={selectedAccount.accountId}
                            experience={selectedExperience}
                            labelClass={labelClass}
                            controlClass={controlClass}
                            showDilly={false}
                        />
                    </>
                }
            </div>
        );
    }
}

interface MiniExperiencesProps {
    availableAccounts: Array<Account>;
    bcApiGateway: BrightcoveApiGateway;
}

interface MiniExperiencesState {
    selectedAccount?: Account;
    selectedExperience?: BCExperience;
    experiencesFilter: string;
    isLoadingExperiences: boolean;
    allExperiences: Array<BCExperience>;
    filteredExperiences: Array<BCExperience>;
}


