import * as React from 'react';
import autobind from 'autobind-decorator';

import { EditVideoTextTracks, MVideo, ActiveTextTrack } from '../components/manage-videos';

export class EditVideoTextTracksStory extends React.Component<EditTextTrackProps, EditTextTrackState> {
    constructor(props: EditTextTrackProps, context?: any) {
        super(props, context);

        this.state = {
            video: {
                'videoId': '5742275941001',
                'referenceId': '5742275941001',
                'sharepointId': 'bcsp-beac-20180316101821-1',
                'name': 'Beach Quickie',
                'shortDescription': '',
                'longDescription': '',
                'state': 'ACTIVE',
                'relatedLinkUrl': '',
                'relatedLinkText': '',
                'creationDate': new Date('2018-02-28T15:05:33.285Z'),
                'economics': 'AD_SUPPORTED',
                'tags': [],
                'customFields': { 'foobar': 'asdf', 'internalname': 'vbn' },
                'textTracks': [
                    {
                        'id': '614b3f5f-67c7-48c3-bc40-d07e8ce3b868',
                        'account_id': '5718741448001',
                        'src': 'http://www.github.com',
                        'srclang': 'ar',
                        'label': 'test123',
                        'kind': 'captions',
                        'mime_type': 'text/vtt',
                        'asset_id': null,
                        'sources': [{ 'src': 'http://www.github.com' }],
                        'default': true
                    }, {
                        'id': 'c6e52eac-9989-414a-b03b-c1ce48fa0997',
                        'account_id': '5718741448001',
                        'src': 'http://house-fastly-signed-us-east-1-prod.brightcovecdn.com/media/v1/text/vtt/clear/5718741448001/a45772df-d591-4ae1-854c-def8e264ab55/c6e52eac-9989-414a-b03b-c1ce48fa0997/text.vtt?fastly_',
                        'srclang': 'en-US',
                        'label': 'asdf',
                        'kind': 'captions',
                        'mime_type': 'text/webvtt',
                        'asset_id': null,
                        'sources': [
                            {
                                'src': 'http://house-fastly-signed-us-east-1-prod.brightcovecdn.com/media/v1/text/vtt/clear/5718741448001/a45772df-d591-4ae1-854c-def8e264ab55/c6e52eac-9989-414a-b03b-c1ce48fa0997/text.vtt?fastly_'
                            },
                            {
                                'src': 'https://house-fastly-signed-us-east-1-prod.brightcovecdn.com/media/v1/text/vtt/clear/5718741448001/a45772df-d591-4ae1-854c-def8e264ab55/c6e52eac-9989-414a-b03b-c1ce48fa0997/text.vtt?fastly_'
                            }
                        ], 'in_band_metadata_track_dispatch_type': '', 'default': false
                    }],
                'videoStillUrl': 'https://cf-images.us-east-1.prod.boltdns.net/v1/jit/5718741448001/a45772df-d591-4ae1-854c-def8e264ab55/main/1280x720/1m8s60ms/match/image.jpg',
                'thumbnailUrl': 'https://cf-images.us-east-1.prod.boltdns.net/v1/jit/5718741448001/a45772df-d591-4ae1-854c-def8e264ab55/main/160x90/1m8s60ms/match/image.jpg'
            }
        };
    }

    @autobind
    onAddTextTrack(textTrack: ActiveTextTrack) {
        const { video } = this.state;

        if (!video.textTracks) {
            video.textTracks = [];
        }

        video.textTracks.push(textTrack);
        this.setState({
            video
        });
    }

    @autobind
    onRemoveTextTrack(textTrack: ActiveTextTrack) {
        const { video } = this.state;

        if (video.textTracks) {
            const ix = video.textTracks.indexOf(textTrack);
            video.textTracks.splice(ix, 1);
            this.setState({
                video
            });
        }
    }

    render() {
        return (
            <EditVideoTextTracks
                video={this.state.video}
                onAddTextTrack={this.onAddTextTrack}
                onRemoveTextTrack={this.onRemoveTextTrack}
            />
        );
    }
}

interface EditTextTrackProps {

}

interface EditTextTrackState {
    video: MVideo;
}



