import { BCAccessTokenService, DynamicIngestApi, ExperiencesApi, PlaylistsApi, VideosApi } from './index';
import { PlayerManagementApi } from './PlayerManagementApi';

export class BrightcoveApiGateway {
    private readonly _accessTokenService: BCAccessTokenService;

    private readonly _dynamicIngestApi: DynamicIngestApi;
    private readonly _experiencesApi: ExperiencesApi;
    private readonly _playerManagementApi: PlayerManagementApi;
    private readonly _playlistsApi: PlaylistsApi;
    private readonly _videosApi: VideosApi;

    private readonly _errorCallback?: (message: string, err?: Error) => boolean | undefined;

    public constructor(accessTokenService: BCAccessTokenService, errorCallback?: (message: string, err?: Error) => boolean | undefined) {
        if (!accessTokenService) {
            throw new Error('accessTokenService must be specified.');
        }

        this._accessTokenService = accessTokenService;
        this._errorCallback = errorCallback;

        this._dynamicIngestApi = new DynamicIngestApi(this._accessTokenService);
        this._experiencesApi = new ExperiencesApi(this._accessTokenService);
        this._playerManagementApi = new PlayerManagementApi(this._accessTokenService);
        this._playlistsApi = new PlaylistsApi(this._accessTokenService);
        this._videosApi = new VideosApi(this._accessTokenService);
    }

    get dynamicIngestApi(): DynamicIngestApi {
        return this._dynamicIngestApi;
    }

    get experiencesApi(): ExperiencesApi {
        return this._experiencesApi;
    }

    get playerManagementApi(): PlayerManagementApi {
        return this._playerManagementApi;
    }

    get playlistsApi(): PlaylistsApi {
        return this._playlistsApi;
    }

    get videosApi(): VideosApi {
        return this._videosApi;
    }

    get accessTokenService(): BCAccessTokenService {
        return this._accessTokenService;
    }
}


