enum DateStringBrand { }
enum UrlStringBrand { }

export type DateString = string & DateStringBrand;
export type UrlString = string & UrlStringBrand;

// #region AccessToken

export interface BCApiCredentials {
    client_id: string;
    client_secret: string;
}

export interface BCAccessTokenService {
    obtainAccessToken(accountId: string): Promise<BCAccessToken>;
    invalidateAccessToken(accountId: string): void;
}

export interface BCAccessToken {
    access_token: string;
    token_type: string;
    expires_in: number;
    expiration: Date;
}

export interface BCGetClientCredentialByIdResponse {
    name: string;
    name_html: string;
    description: string;
    description_html: string;
    client_id: string;
    client_secret: string;
    type: string;
    issued_to: string;
    issued_user: string;
    redirect_url: string;
    trusted: boolean;
    revoked: boolean;
    expires_at: DateString;
    issued_at: DateString;
    maximum_scope: Array<{
        type: string;
        'account-id': string;
        operations: Array<string>;
    }>;
}
// #endregion

// #region Video
export interface BCVideo {
    id: string;
    name: string;
    complete: boolean;
    created_at: string;
    ad_keys: string;
    clip_source_video_id: string;
    custom_fields: { [fieldName: string]: string };
    cuePoints: {
        name: string;
        type: string;
        time: number;
        metadata: string;
        'force-stop': string;
    };
    delivery_type: string;
    description: string;
    duration: number;
    digital_master_id: string;
    economics: string;
    folder_id: string;
    geo: {
        countries: string;
        exclude_countries: boolean;
        restricted: boolean;
    };
    has_digital_master: boolean;
    images: {
        poster: {
            asset_id: string;
            sources: Array<{ src: UrlString }>;
            src: UrlString;
        };
        thumbnail: {
            asset_id: string;
            sources: Array<{ src: UrlString }>;
            src: UrlString;
        };
        src: string;
    };
    link: {
        text: string;
        url: UrlString | string;
    };
    long_description: string;
    offline_enabled: boolean;
    original_filename: string;
    projection: string;
    published_at: DateString;
    reference_id: string;
    schedule: {
        starts_at: string | null;
        ends_at: string | null;
    };
    state: string;
    sharing: {
        by_external_acct: boolean;
        by_id: string;
        source_id: string;
        to_external_acct: boolean;
        by_reference: boolean;
    };
    tags: Array<string>;
    text_tracks: Array<BCVideoTextTrack>;
    updated_at: DateString;
}

export enum BCVideoTextTrackKind {
    Captions = 'captions',
    Subtitles = 'subtitles',
    Descriptions = 'descriptions',
    Chapters = 'chapters',
    Metadata = 'metadata'
}

export interface BCVideoTextTrack {
    id: string;
    account_id: string;
    src: string;
    srclang: string;
    label: string;
    kind: string;
    mime_type: string;
    asset_id: string | null;
    sources: Array<{
        src: string;
    }>;
    in_band_metadata_track_dispatch_type?: string;
    default: boolean;
}

export interface BCGetVideosParams {
    limit?: number;
    offset?: number;
    q?: string;
    sort?: string;
}

export interface BCGetVideoCountParams {
    q?: string;
    sort?: string;
}

export interface BCGetVideoCountResponse {
    count: number;
}
// #endregion

// #region Custom Fields
export interface BCGetCustomFieldsResponse {
    custom_fields: Array<BCCustomField>;
    max_custom_fields: string;
    standard_fields: Array<{
        description: string;
        id: string;
        required: boolean;
    }>;
}

export interface BCCustomField {
    description: string;
    display_name: string;
    enum_values: string[];
    id: string;
    required: boolean;
    type: string;
}
// #endregion

// #region Ingest
export interface BCGetUploadUrlsResponse {
    bucket: string;
    object_key: string;
    access_key_id: string;
    secret_access_key: string;
    session_token: string;
    signed_url: string;
    api_request_url: string;
}

export interface BCMakeIngestRequestResponse {
    id: string;
}

export interface BCIngestTextTrack {
    url: string;
    srclang: string;
    kind: string;
    label: string;
    default: boolean;
}

export interface BCIngestJob {
    account_id: string;
    error_code: string;
    error_message: string;
    id: string;
    priority: string;
    started_at: DateString;
    state: string;
    submitted_at: DateString;
    updated_at: DateString;
    video_id: string;
}
// #endregion

// #region Playlist
export interface BCPlaylist {
    id: string;
    name: string;
    type: BCPlaylistType;
    description: string;
    account_id: string;
    favorite: boolean;
    reference_id: string | null;
    created_at?: DateString;
    updated_at?: DateString;
    limit?: number;
    video_ids?: Array<string>;
    search: string | null;
}

export enum BCPlaylistType {
    Manual = 'EXPLICIT',
    ActivatedDateOldestFirst = 'ACTIVATED_OLDEST_TO_NEWEST',
    ActivatedDateNewestFirst = 'ACTIVATED_NEWEST_TO_OLDEST',
    Alphabetical = 'ALPHABETICAL',
    TotalPlays = 'PLAYS_TOTAL',
    TrailingWeekPlays = 'PLAYS_TRAILING_WEEK',
    StartDateOldestFirst = 'START_DATE_OLDEST_TO_NEWEST',
    StartDateNewestFirst = 'START_DATE_NEWEST_TO_OLDEST'
}

export interface BCGetPlaylistsParams {
    limit?: number;
    offset?: number;
    q?: string;
    sort?: string;
}

export interface BCCreatePlaylistParams {
    name: string;
    description?: string;
    reference_id?: string;
    type: BCPlaylistType;
    limit?: string;
    video_ids?: Array<string>;
    search?: string;
}

export interface BCUpdatePlaylistParams {
    name: string;
    description?: string;
    reference_id?: string;
    type: BCPlaylistType;
    limit?: string;
    video_ids?: Array<string>;
    search?: string;
}
// #endregion

// #region Experiences
export interface BCExperience {
    accountId: string;
    analytics: {
        gallery: {
            projectId: string;
            writeKey: string;
        }
        googleAnalyticsId: string | null;
    };
    assets: {
        customCss: string | null;
        customCssUrl: string | null;
        customJavaScript: string | null;
        customJavaScriptUrl: string | null;
        fonts: Array<string>;
        headerAdCode: string | null;
    };
    configurations: any;
    content: any;
    createdAt: DateString;
    description: string;
    hasChanges: boolean;
    height: number | null;
    id: string;
    layouts: {
        presentation: {
            all: string;
            during: string;
        };
    };
    locale: string;
    name: string;
    playbackApiBaseUrl: string | null;
    player: {
        autoAdvance: boolean;
        autoAdvanceTime: string | null;
        baseUrl: string | null;
        endBehavior: string;
        id: string;
        playsInline: boolean;
    };
    policyKey: string;
    previewUrl: string;
    publishedAt: DateString;
    publishedUrl: string;
    social: {
        deepLinkParameter: string | null;
        display: string;
        sharingOptions: any;
    };
    styles: any;
    template: string;
    templateVersion: string;
    templateVersionLocked: boolean;
    theme: string | null;
    updatedAt: DateString;
    videos: {
        allowDownloads: string;
        downloadCustomField: string;
        downloadCustomFieldValue: string;
        playlistId: string | null;
        showRelatedLinks: boolean;
        type: string;
        useLongDescriptions: boolean;
        videoIds: Array<string>;
        watchedIndicator: boolean;
    };
    width: number | null;
    youtubeKey: string;
}

export interface BCGetExperiencesResponse {
    items: Array<BCExperience>;
}
// #endregion

// #region Player Management
export interface BCPlayer {
    account_id: string;
    branches: {
        master: BCPlayerBranch & {
            master_url: string;
        };
        preview:  BCPlayerBranch & {
            preview_url: string;
        };
    };
    created_at: string;
    description: string;
    embed_count: string;
    id: string;
    name: string;
    url: string;
}

export interface BCPlayerBranch {
    configuration: {
        autoadvance: boolean;
        autoplay: boolean;
        css: {
            controlBarColor: any;
            controlColor: any;
            progressColor: any;
        };
        errors: boolean;
        fullScreenControl: boolean;
    };
    languages: Array<string>;
    loop: boolean;
    media: {
        sources: Array<{
            height: string;
            highres: string;
            src: string;
            title: string;
            tracks: Array<BCVideoTextTrack>;
            type: string;
            width: string;
        }>;
        src: string;
        tracks: Array<BCVideoTextTrack>;
        player: {
            inactive: boolean;
            template: {
                name: string;
                version: string;
            };
            playlist: boolean;
            playOnSelect: boolean;
            plugins: Array<{
                name: string;
                options: any;
            }>;
            preload: string;
            scripts: string;
            skin: boolean;
            studio_configuration: {
                player: {
                    adjusted: boolean;
                    height: string;
                    width: string;
                };
            };
            stylesheets: Array<string>;
            techOrder: Array<string>;
            video_cloud: {
                policy_key: string;
                video: string;
            };
            template_updated_at: string;
            updated_at: string;
        };
    };
}

export interface BCGetAllPlayersResponse {
    items: Array<BCPlayer>;
    item_count: number;
}
// #endregion


