import { Web, sp } from 'sp-pnp-js';

export class SPGroups {
    private readonly addinWebUrl: string;
    private readonly hostWebUrl: string;
    private readonly web: Web;

    constructor(addInWebUrl: string, hostWebUrl: string) {

        this.addinWebUrl = addInWebUrl;
        this.hostWebUrl = hostWebUrl;
        this.web = sp.crossDomainWeb(addInWebUrl, hostWebUrl);
    }

    public async getGroups(): Promise<Array<SPGroup>> {
        let result: Array<SPGroup> = [];

        let groups = await this.web.siteGroups.get();
        for (let group of groups) {
            result.push({
                id: group.Id,
                title: group.Title
            });
        }

        return result;
    }
}

export interface SPGroup {
    id: number;
    title: string;
}


