import {
    BCAccessTokenService,
    BCGetUploadUrlsResponse,
    BCMakeIngestRequestResponse,
    BCVideoTextTrack,
    BCIngestTextTrack,
} from './index';

import { IngestEndpointUrl } from './Endpoints';
import * as uuidv4 from 'uuid/v4';

/**
 * See
 * https://support.brightcove.com/source-file-upload-api-dynamic-ingest#cmsRequest
 * https://brightcovelearning.github.io/Brightcove-API-References/dynamic-ingest-api/v1/doc/index.html
 */

export class DynamicIngestApi {
    private readonly accessTokenService: BCAccessTokenService;

    constructor(accessTokenService: BCAccessTokenService) {
        this.accessTokenService = accessTokenService;
    }

    public async getUploadUrls(accountId: string, videoId: string): Promise<BCGetUploadUrlsResponse> {

        const accessToken = await this.accessTokenService.obtainAccessToken(accountId);
        let url = `${IngestEndpointUrl}/${accountId}/videos/${videoId}/upload-urls/bc-sp-${uuidv4()}`;

        let response = await fetch(url, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }

    public async makeIngestRequest(accountId: string, videoId: string, apiRequestUrl: string): Promise<BCMakeIngestRequestResponse> {
        const accessToken = await this.accessTokenService.obtainAccessToken(accountId);
        let url = `${IngestEndpointUrl}/${accountId}/videos/${videoId}/ingest-requests`;

        let response = await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            },
            body: JSON.stringify({
                master: {
                    url: apiRequestUrl
                },
                profile: 'multi-platform-standard-static',
                'capture-images': true
            })
        });

        return await response.json();
    }

    public async addTextTracks(accountId: string, videoId: string, textTracks: Array<Partial<BCVideoTextTrack>>): Promise<BCMakeIngestRequestResponse> {
        const accessToken = await this.accessTokenService.obtainAccessToken(accountId);
        let url = `${IngestEndpointUrl}/${accountId}/videos/${videoId}/ingest-requests`;

        let newTracks: Array<BCIngestTextTrack> = [];

        for (let tt of textTracks) {
            newTracks.push({
                url: tt.src || '',
                srclang: tt.srclang || '',
                kind: tt.kind || '',
                label: tt.label || '',
                default: !!tt.default
            });
        }
        
        let response = await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            },
            body: JSON.stringify({
                profile: 'multi-platform-standard-static',
                text_tracks: newTracks
            })
        });

        return await response.json();
    }
}


