import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { Button } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn, SelectRow } from 'react-bootstrap-table';
import { AccountPicker, PlayerPicker, Spinner } from '../common';
import { PlayerEmbedCodeControls, PlayerEmbedCodeProps, getDefaultPlayerEmbedCodeProps, PlayerEmbedCode } from '../embeds';
import { PlaylistSearchControls, PlaylistSearchParameters, getDefaultPlaylistSearchParameters, SortOrder, SortField } from '../manage-playlists';

import autobind from 'autobind-decorator';

import { Account } from '../../services/sharepoint';
import { BrightcoveApiGateway, BCPlayer, BCPlaylist, BCGetPlaylistsParams } from '../../services/brightcove';

export class MiniPlaylists extends React.Component<MiniPlaylistsProps, MiniPlaylistsState> {
    constructor(props: MiniPlaylistsProps, context?: any) {
        super(props, context);

        let searchParameters = getDefaultPlaylistSearchParameters();

        this.state = {
            playerFilter: '',
            playerEmbedCode: getDefaultPlayerEmbedCodeProps(),
            searchParameters,
            isLoadingPlaylists: false,
            playlists: []
        };
    }
    
    @autobind
    async componentDidUpdate(prevProps: MiniPlaylistsProps, prevState: MiniPlaylistsState) {
        const { selectedAccount, searchParameters } = this.state;

        if (selectedAccount !== prevState.selectedAccount) {
            await this.searchForPlaylists(searchParameters);
        }
    }

    @autobind
    async searchForPlaylists(searchParams: Partial<PlaylistSearchParameters>): Promise<void> {
        const { selectedAccount } = this.state;
        if (!selectedAccount || !selectedAccount.accountId) {
            return;
        }

        this.setState({
            isLoadingPlaylists: true
        });

        const { bcApiGateway } = this.props;
        const playlistsApi = bcApiGateway.playlistsApi;

        // Map search params to 'q'
        let bcParams: BCGetPlaylistsParams = {
            limit: searchParams.searchLimit || 45,
            sort: searchParams.sortOrder === SortOrder.Descending ? '-' : '',
            q: searchParams.searchTerms ? `+"${searchParams.searchTerms}"` : ''
        };

        switch (searchParams.sortField) {
            case SortField.DisplayName:
                bcParams.sort += 'name';
                break;
            case SortField.ModifiedDate:
                bcParams.sort += 'updated_at';
                break;
            default:
                // Do nothing.
                break;
        }

        const playlists = await playlistsApi.getPlaylists(selectedAccount.accountId, bcParams);

        this.setState({
            isLoadingPlaylists: false,
            playlists
        });
    }

    @autobind
    handleAccountChange(selectedAccount: Account) {
        this.setState({
            selectedAccount
        });
    }

    @autobind
    handlePlayerFilterChange(value: string) {
        this.setState({
            playerFilter: value
        });
    }

    @autobind
    handlePlayerChange(selectedPlayer: BCPlayer) {
        this.setState({
            selectedPlayer
        });
    }

    @autobind
    handlePlayerEmbedCodeChanged(playerEmbedCode: Partial<PlayerEmbedCodeProps>) {
        this.setState({
            playerEmbedCode
        });
    }

    @autobind
    handleSearchParametersChanged(searchParameters: Partial<PlaylistSearchParameters>, fieldChanged: string) {
        this.setState({
            searchParameters
        });

        if (fieldChanged !== 'searchTerms') {
            this.searchForPlaylists(searchParameters);
        }
    }

    @autobind
    playlistSelected(playlist: BCPlaylist, isSelected: boolean, e: any) {
        let selectedPlaylist: BCPlaylist | undefined;
        if (isSelected) {
            selectedPlaylist = playlist;
        }
        this.setState({
            selectedPlaylist
        });
    }

    @autobind
    renderPlaylistNameCell(cell: any, playlist: BCPlaylist, extraData: { selectedAccount: Account }) {
        return (
            <div className="row">
                <div className="col-sm-12">
                    <a href="#">{playlist.name}</a>
                    <div className="small">{playlist.id}</div>
                </div>
            </div>
        );
    }

    renderPlaylistEmbed(selectedAccount: Account, selectedPlayer: BCPlayer, selectedPlaylist: BCPlaylist, playerEmbedCodeProps: Partial<PlayerEmbedCodeProps>) {

        const playerPreviewUrl = `https://players.brightcove.net/${selectedAccount.accountId}/${selectedPlayer.id}_default/index.html?playlistId=${selectedPlaylist.id}`;
        const embedCode = ReactDOMServer.renderToStaticMarkup(<PlayerEmbedCode accountId={selectedAccount.accountId} playerId={selectedPlayer && selectedPlayer.id} mode="Playlist" modeId={selectedPlaylist.id} {...playerEmbedCodeProps as PlayerEmbedCodeProps} />);

        const labelClass = 'col-md-3 control-label small';
        const controlClass = 'col-md-9';

        return (
            <>
                <div className="form-group">
                    <label className={labelClass}>Preview in browser</label>
                    <div className={controlClass + ' word-wrap'}>
                        <a href={playerPreviewUrl} target="_blank">{playerPreviewUrl}</a>
                    </div>
                </div>
                <div className="col-md-12">
                    <textarea style={{ width: '100%', height: '300px' }} value={embedCode} onChange={() => null} />
                </div>
            </>
        );

    }

    render() {
        const { availableAccounts, bcApiGateway } = this.props;
        const {
            selectedAccount,
            selectedPlayer,
            playerFilter,
            playerEmbedCode,
            searchParameters,
            isLoadingPlaylists,
            playlists,
            selectedPlaylist
        } = this.state;

        const labelClass = 'col-md-3 control-label small';
        const controlClass = 'col-md-9';

        const selectRow: SelectRow = {
            mode: 'radio',
            clickToSelect: true,
            className: 'info',
            onSelect: this.playlistSelected
        };

        return (
            <div className="form-horizontal">
                <div className="form-group">
                    <label className={labelClass}>Brightcove Account:</label>
                    <div className={controlClass}>
                        <AccountPicker className="input-sm" availableAccounts={availableAccounts} selectedAccount={selectedAccount} onChange={this.handleAccountChange} />
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Player Filter:</label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <input
                                type="search"
                                placeholder="Search"
                                className="form-control input-sm"
                                value={playerFilter}
                                onChange={(e) => this.handlePlayerFilterChange(e.target.value)}
                            />
                            <div className="input-group-btn searchButton">
                                <Button bsSize="small"><i className="glyphicon glyphicon-search" /></Button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Player:</label>
                    <div className={controlClass}>
                        <PlayerPicker className="input-sm" bcApiGateway={bcApiGateway} accountId={selectedAccount && selectedAccount.accountId} playerFilter={playerFilter} selectedPlayerId={selectedPlayer && selectedPlayer.id} onChange={this.handlePlayerChange} />
                    </div>
                </div>
                <PlayerEmbedCodeControls
                    labelClass={labelClass}
                    controlClass={controlClass}
                    formControlClass="input-sm"
                    playerEmbedCode={playerEmbedCode}
                    onPlayerEmbedCodeChanged={this.handlePlayerEmbedCodeChanged}
                />
                <PlaylistSearchControls
                    labelClass={labelClass}
                    controlClass={controlClass}
                    formControlClass="input-sm"
                    searchParameters={searchParameters}
                    onSearchParametersChanged={this.handleSearchParametersChanged}
                    onSearchForPlaylists={this.searchForPlaylists}
                />
                {isLoadingPlaylists && selectedAccount &&
                    <Spinner loadingText="Loading Playlists..." />
                }
                {!isLoadingPlaylists && selectedAccount &&
                    <div className="well" style={{ height: '250px', overflow: 'auto', margin: '0 15px' }} >
                        <BootstrapTable data={playlists} striped={true} bordered={false} selectRow={selectRow}>
                            <TableHeaderColumn dataField="name" dataFormat={this.renderPlaylistNameCell} formatExtraData={{ selectedAccount }}>Playlist</TableHeaderColumn>
                            <TableHeaderColumn dataField="id" isKey={true} hidden={true}>ID</TableHeaderColumn>
                        </BootstrapTable>
                        <hr />
                    </div>
                }
                {selectedAccount && selectedPlayer && selectedPlaylist &&
                    this.renderPlaylistEmbed(selectedAccount, selectedPlayer, selectedPlaylist, playerEmbedCode)
                }
            </div>
        );
    }
}

interface MiniPlaylistsProps {
    availableAccounts: Array<Account>;
    bcApiGateway: BrightcoveApiGateway;
}

interface MiniPlaylistsState {
    selectedAccount?: Account;
    selectedPlayer?: BCPlayer;
    playerFilter: string;
    playerEmbedCode: Partial<PlayerEmbedCodeProps>;
    searchParameters: Partial<PlaylistSearchParameters>;

    isLoadingPlaylists: boolean;
    playlists: Array<BCPlaylist>;
    selectedPlaylist?: BCPlaylist;
}


