import * as React from 'react';
import { FormControl } from 'react-bootstrap';
import autobind from 'autobind-decorator';

import { BCPlayer, BrightcoveApiGateway } from '../../services/brightcove';

import { find, filter, orderBy } from 'lodash';

export class PlayerPicker extends React.Component<PlayerPickerProps, PlayerPickerState> {
    private _mounted: boolean = false;

    constructor(props: PlayerPickerProps, context?: any) {
        super(props, context);

        this.state = {
            isLoadingPlayers: true,
            allPlayers: [],
            filteredPlayers: []
        };
    }

    async componentDidMount() {
        this._mounted = true;
    }

    async componentWillReceiveProps(nextProps: PlayerPickerProps) {
        const { accountId, playerFilter, bcApiGateway, onChange } = nextProps;
        const { playerManagementApi } = bcApiGateway;

        if (!accountId) {
            this.setState({
                isLoadingPlayers: false
            });
            return;
        }

        let allPlayers = this.state.allPlayers;

        if (allPlayers.length > 0 && accountId === this.props.accountId && playerFilter === this.props.playerFilter) {
            return;
        }

        // Get the players from the accounts list.
        if (nextProps.accountId !== this.props.accountId || allPlayers.length === 0) {
            const playersResponse = await playerManagementApi.getAllPlayers(accountId);
            allPlayers = playersResponse.items;
            this.setState({
                allPlayers
            });
        }

        if (!this._mounted) {
            return;
        }

        // If a player filter string is specified, filter the players by it.
        let filteredPlayers = allPlayers;
        if (playerFilter && playerFilter.length > 0) {
            const strFilter = playerFilter.toLowerCase();
            filteredPlayers = filter(filteredPlayers, (player) => player.name && player.name.toLowerCase().indexOf(strFilter) > -1);
        }

        let selectedPlayer: BCPlayer | undefined = undefined;
        if (filteredPlayers && filteredPlayers.length > 0) {
            selectedPlayer = find(filteredPlayers, { id: 'default' });
            if (!selectedPlayer) {
                selectedPlayer = filteredPlayers[0];
            }
            if (selectedPlayer && onChange) {
                onChange(selectedPlayer);
            }
        }

        // Sort the players alphabetically by namewith the default player on top.
        filteredPlayers = orderBy(filteredPlayers, ['name']);
        const defaultPlayerIndex = filteredPlayers.findIndex((p) => p.id === 'default');
        if (defaultPlayerIndex > 0) {
            const defaultPlayer = filteredPlayers.splice(defaultPlayerIndex, 1);
            filteredPlayers.splice(0, 0, defaultPlayer[0]);
        }

        this.setState({
            isLoadingPlayers: false,
            filteredPlayers,
            selectedPlayer
        });
    }

    @autobind
    handlePlayerChange(event: React.FormEvent<FormControl>) {

        const { onChange } = this.props;
        const { allPlayers } = this.state;
        const target = event.target as any;

        const selectedPlayer = find(allPlayers, { id: target.value });

        if (selectedPlayer && onChange) {
            onChange(selectedPlayer);
        }
    }

    render() {
        const { placeholder, className, accountId, selectedPlayerId, playerFilter } = this.props;
        const { isLoadingPlayers, filteredPlayers } = this.state;

        if (!accountId) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                >
                    <option>Please select an account...</option>
                </FormControl>
            );
        }

        if (isLoadingPlayers) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                >
                    <option>Loading Players...</option>
                </FormControl>
            );
        }

        if (!filteredPlayers && playerFilter && playerFilter.length > 0) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                >
                    <option>All players were filtered out</option>
                </FormControl>
            );
        }

        let bcPlaceholder = placeholder;
        if (!bcPlaceholder) {
            bcPlaceholder = 'Select a player...';
        }

        let bcPlayerId = selectedPlayerId;
        if (!bcPlayerId) {
            bcPlayerId = 'default';
        }

        return (
            <FormControl
                className={className}
                componentClass="select"
                placeholder={bcPlaceholder}
                value={bcPlayerId}
                onChange={this.handlePlayerChange}
            >
                {filteredPlayers.map((player, i) =>
                    <option key={i} value={player.id}>{player.name}</option>
                )
                }
            </FormControl>
        );
    }
}

export interface PlayerPickerProps {
    className?: string;
    placeholder?: string;
    bcApiGateway: BrightcoveApiGateway;
    accountId?: string;
    playerFilter?: string;
    selectedPlayerId?: string;
    onChange?: (selectedPlayer: BCPlayer) => void;
}

export interface PlayerPickerState {
    isLoadingPlayers: boolean;
    allPlayers: Array<BCPlayer>;
    filteredPlayers: Array<BCPlayer>;
    selectedPlayer?: BCPlayer;
}


