import * as React from 'react';
import Dropzone from 'react-dropzone';
import { ImageFile } from 'react-dropzone';
import { Prompt } from 'react-router';
import { Button, ProgressBar } from 'react-bootstrap';
import { AccountPicker } from '../common';

import { BrightcoveApiGateway, BCVideo, BCGetUploadUrlsResponse, BCMakeIngestRequestResponse } from '../../services/brightcove';
import { SPListGateway, Account } from '../../services/sharepoint';
import { ConnectorContext } from '../../Common';

import { filter } from 'lodash';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { IconName } from '@fortawesome/fontawesome';
import autobind from 'autobind-decorator';
import './AddVideos.css';

export class AddVideos extends React.Component<AddVideosProps, AddVideosState> {
    constructor(props: AddVideosProps, context?: any) {
        super(props, context);

        this.state = {
            videoFiles: []
        };
    }

    @autobind
    handleAccountChange(selectedAccount: Account) {
        this.setState({
            selectedAccount
        });
    }

    @autobind
    onDrop(acceptedFiles: ImageFile[], rejectedFiles: ImageFile[]) {

        const { selectedAccount, videoFiles } = this.state;

        if (!selectedAccount) {
            throw new Error('An account must be selected.');
        }

        acceptedFiles.forEach(async file => {
            const uploadingVideo: UploadingVideo = {
                file,
                account: selectedAccount,
                fileName: file.name,
                isUploading: true,
                hasError: false,
                status: 'Starting Upload...',
                progress: 10,
            };

            videoFiles.push(uploadingVideo);
            this.performDynamicIngest(uploadingVideo);
        });

        this.setState({
            videoFiles: [...videoFiles]
        });
    }

    updateVideoStatus(video: UploadingVideo) {
        this.setState({
            videoFiles: [...this.state.videoFiles]
        });
    }

    async performDynamicIngest(video: UploadingVideo): Promise<void> {

        if (!video) {
            throw new Error('A video must be specified.');
        }

        const { dynamicIngestApi } = this.props.bcApiGateway;

        // Start the upload process.
        // 1. Create a new video.

        video.status = `Creating Brightcove video for ${video.fileName}...`;
        video.progress = 10;
        this.updateVideoStatus(video);

        try {
            const videosApi = this.props.bcApiGateway.videosApi;
            const createdVideo = await videosApi.createVideo(video.account.accountId, video.fileName);
            video.bcVideo = createdVideo;
        } catch (ex) {
            video.hasError = true;
            video.status = 'An error has occurred while creating the Brightcove Video: ' + ex.message;
            video.isUploading = false;
            video.progress = 100;
            return;
        } finally {
            this.updateVideoStatus(video);
        }

        // 2. Get the AWS S3 Urls for the upload.

        video.status = `Getting AWS S3 Urls for ${video.bcVideo.id}...`;
        video.progress = 20;
        this.updateVideoStatus(video);

        try {
            video.uploadUrls = await dynamicIngestApi.getUploadUrls(video.account.accountId, video.bcVideo.id);
        } catch (ex) {
            video.hasError = true;
            video.status = 'An error has occurred while obtaining upload urls: ' + ex.message;
            video.isUploading = false;
            video.progress = 100;
            return;
        } finally {
            this.updateVideoStatus(video);
        }

        // 3. Perform the upload to AWS S3.
        video.status = `Uploading '${video.fileName}' (${video.bcVideo.id}) to AWS (this may take awhile)...`;
        video.progress = 40;
        this.updateVideoStatus(video);

        try {
            const uploadResponse = await fetch(video.uploadUrls.signed_url, {
                method: 'PUT',
                credentials: 'omit',
                headers: {
                    'Content-Type': ''
                },
                body: video.file
            });
            video.uploadResponse = uploadResponse;
        } catch (ex) {
            video.hasError = true;
            video.status = `An error has occurred while uploading a video to AWS: ` + ex.message;
            video.isUploading = false;
            video.progress = 100;
            return;
        } finally {
            this.updateVideoStatus(video);
        }

        // 4. Make an ingest request.
        video.status = `Making an ingest request for ${video.bcVideo.id}...`;
        video.progress = 90;
        this.updateVideoStatus(video);

        try {
            const makeIngestResponse = await dynamicIngestApi.makeIngestRequest(video.account.accountId, video.bcVideo.id, video.uploadUrls.api_request_url);
            video.ingestResponse = makeIngestResponse;
        } catch (ex) {
            video.hasError = true;
            video.status = `An error has occurred while making an ingest request: ` + ex.message;
            video.isUploading = false;
            video.progress = 100;
            return;
        } finally {
            this.updateVideoStatus(video);
        }

        video.status = 'Upload Complete! (Video may still be processing)';
        video.isUploading = false;
        video.progress = 100;
        this.updateVideoStatus(video);
    }

    render() {

        let dropzoneRef: Dropzone | null;
        const uploadIconName: IconName = 'arrow-circle-up';

        const { connectorContext } = this.props;
        const { videoFiles, selectedAccount } = this.state;
        const uploadingVideoFiles = filter(videoFiles, { isUploading: true });

        return (
            <>
                <ol className="breadcrumb">
                    <li><a href="#/">Brightcove Management</a></li>
                    <li className="active">Add Videos</li>
                </ol>
                <div className="formContent">
                    <label>Select Account:</label>{' '}
                    <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccount={selectedAccount} onChange={this.handleAccountChange} />
                </div>
                {selectedAccount &&
                    <>
                        <Dropzone className="dropzone" ref={(node) => { dropzoneRef = node; }} onDrop={this.onDrop} disableClick={true} style={{ textAlign: 'center', marginTop: '5px' }}>
                            <div className="drag-videos-icon">
                                <FontAwesomeIcon icon={uploadIconName} style={{ color: '#d32781' }} size="5x" />
                            </div>
                            <h5 className="drag-videos-target">Drag Your Videos Here</h5>
                            <div className="drag-or-label dimmable">-or-</div>
                            <Button bsStyle="primary" onClick={() => { dropzoneRef && dropzoneRef.open(); }}>Browse for Videos</Button>
                        </Dropzone>
                        <table className="table table-striped">
                            <tbody>
                                {videoFiles.map((video, ix) =>
                                    <tr key={ix} className="row">
                                        <td>
                                            <div>
                                                <div className="result">{video.fileName}</div>
                                                <div>
                                                    <ProgressBar bsStyle={video.hasError ? 'danger' : 'info'} now={video.progress} striped={true} active={video.isUploading} />
                                                    <div style={{ fontSize: 'small' }}>
                                                        <span>{video.status}</span>
                                                        {video.progress === 100 && video.bcVideo &&
                                                            <Button bsStyle="link" href={`${window.location.origin}${window.location.pathname}#/manage-videos/edit/${video.account.accountId}/Brightcove/${video.bcVideo.id}`} target="_blank">Edit Video</Button>
                                                        }
                                                    </div>
                                                </div>
                                            </div>
                                        </td>
                                    </tr>
                                )}
                            </tbody>
                        </table>
                        <Prompt
                            when={uploadingVideoFiles && uploadingVideoFiles.length > 0}
                            message="There are videos currently uploading. Are you sure you wish to leave?"
                        />
                    </>
                }
            </>
        );
    }
}

interface AddVideosProps {
    connectorContext: ConnectorContext;
    bcApiGateway: BrightcoveApiGateway;
    spListGateway: SPListGateway;
    onAllowNavigateChange?: (allowNavigate: boolean) => void;
}

interface UploadingVideo {
    file: ImageFile;
    account: Account;
    fileName: string;

    isUploading: boolean;
    hasError: boolean;
    status: string;
    progress: number;

    bcVideo?: BCVideo;
    uploadUrls?: BCGetUploadUrlsResponse;
    uploadResponse?: Response;
    ingestResponse?: BCMakeIngestRequestResponse;
}

interface AddVideosState {
    videoFiles: Array<UploadingVideo>;
    accounts?: Array<Account>;
    selectedAccount?: Account;
}



