export * from './ManagePlaylists';
export * from './EditPlaylist';
export * from './PlaylistSearchControls';

export enum SortField {
    DisplayName = 'name',
    ModifiedDate = 'modifiedDate',
}

export enum SortOrder {
    Ascending = 'ascending',
    Descending = 'descending'
}

export interface PlaylistSearchParameters {
    searchTerms: string;
    searchLimit: number;
    folderId: string;
    sortField: SortField;
    sortOrder: SortOrder;
}

export function getDefaultPlaylistSearchParameters() {
    return {
        searchTerms: '',
        searchLimit: 30,
        sortField: SortField.DisplayName,
        sortOrder: SortOrder.Ascending,
    };
}


