import * as React from 'react';

export class PlayerEmbedCode extends React.Component<PlayerEmbedCodeProps> {
    render() {
        const { accountId, mode, modeId, sizing, aspectRatio, playerWidth, autoplay, muted } = this.props;
        let { playerId, playerHeight, units } = this.props;
        playerId = playerId || 'default';
        units = units || 'px';

        const scriptUrl = `//players.brightcove.net/${accountId}/{${playerId}_default/index.min.js`;

        const videoProps: any = {
            'data-account': accountId,
            'data-player': playerId,
            'data-embed': 'default',
            'data-application-id': '',
            className: 'video-js',
            controls: true,
            autoPlay: autoplay,
            muted: muted,
        };

        switch (mode) {
            case 'Playlist':
                videoProps['data-playlist-id'] = modeId;
                break;
            case 'Video':
                videoProps['data-video-id'] = modeId;
                break;
            default:
                throw new Error(`Unsupported mode: ${mode}`);
        }

        if (sizing === 'Fixed') {
            if (playerWidth) {
                switch (aspectRatio) {
                    case '16:9':
                        playerHeight = (parseInt(playerWidth, 10) * 0.5625).toString();
                        break;
                    case '4:3':
                        playerHeight = (parseInt(playerWidth, 10) * 0.75).toString();
                        break;
                    default:
                        // Do Nothing.
                        break;
                }
            }

            if (units === 'px') {
                videoProps.width = playerWidth;
                videoProps.height = playerHeight;
            } else {
                if (playerWidth) {
                    videoProps.style.width = playerWidth + units;
                }

                if (playerHeight) {
                    videoProps.style.height = playerHeight + units;
                }
            }

            return (
                <>
                    <video {...videoProps} />
                    <script src={scriptUrl} />
                </>
            );
        }

        // Responsive.
        videoProps.style = {
            position: 'absolute',
            top: '0px',
            right: '0px',
            bottom: '0px',
            left: '0px',
            width: '100%',
            height: '100%'
        };

        let maxWidth = '100%';
        if (playerWidth) {
            maxWidth = playerWidth + units;
        }

        let topPadding = '0%';
        switch (aspectRatio) {
            case '16:9':
                if (playerWidth) {
                    playerHeight = (parseInt(playerWidth, 10) * 0.5625).toString();
                }
                break;
            case '4:3':
                if (playerWidth) {
                    playerHeight = (parseInt(playerWidth, 10) * 0.75).toString();
                }
                break;
            default:
                // Do Nothing
                break;
        }

        if (playerWidth && playerHeight) {
            topPadding = (parseInt(playerHeight, 10) / parseInt(playerWidth, 10) * 100).toPrecision(4) + '%';
        }

        return (
            <div style={{ position: 'relative', display: 'block', maxWidth: maxWidth }}>
                <div style={{ paddingTop: topPadding }}>
                    <video
                        {...videoProps}
                    />
                    <script src={scriptUrl} />
                </div>
            </div >
        );
    }
}

export function getDefaultPlayerEmbedCodeProps(): Partial<PlayerEmbedCodeProps> {
    return {
        autoplay: false,
        muted: false,
        embedType: 'Standard',
        sizing: 'Responsive',
        aspectRatio: '16:9',
        playerWidth: '',
        playerHeight: '',
        units: 'px'
    };
}

export interface PlayerEmbedCodeProps {
    accountId: string;
    mode: 'Video' | 'Playlist';
    modeId: string;
    playerId?: string;
    autoplay: boolean;
    muted: boolean;
    embedType: 'Standard' | 'Advanced';
    sizing: 'Responsive' | 'Fixed';
    aspectRatio: '16:9' | '4:3' | 'Custom';
    playerWidth?: string;
    playerHeight?: string;
    units?: 'px' | 'em' | '%';
}


