export * from './ManageVideos';
export * from './EditVideo';
export * from './EditVideoTextTracks';
export * from './VideoSearchControls';

export * from './mVideo';
export * from './TextTrackTypes';

export enum SearchTarget {
    SharePoint = 'SharePoint',
    VideoCloud = 'VideoCloud',
}

export enum SortField {
    Name = 'name',
    Relevance = 'relevance',
    CreationDate = 'creationDate',
    StartDate = 'startDate'
}

export enum SortOrder {
    Ascending = 'ascending',
    Descending = 'descending'
}

export interface VideoSearchParameters {
    searchTerms: string;
    searchTarget: SearchTarget;
    searchLimit: number;
    folderId: string;
    sortField: SortField;
    sortOrder: SortOrder;
}

export function getDefaultVideoSearchParameters() {
    return {
        searchTerms: '',
        searchTarget: SearchTarget.SharePoint,
        searchLimit: 30,
        sortField: SortField.Name,
        sortOrder: SortOrder.Ascending,
    };
}


