import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { Button } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn, SelectRow } from 'react-bootstrap-table';
import { AccountPicker, PlayerPicker, Spinner } from '../common';
import { PlayerEmbedCodeControls, PlayerEmbedCodeProps, getDefaultPlayerEmbedCodeProps, PlayerEmbedCode } from '../embeds';
import { VideoSearchControls, VideoSearchParameters, getDefaultVideoSearchParameters, SearchTarget, SortOrder, SortField } from '../manage-videos';

import autobind from 'autobind-decorator';

import { Account } from '../../services/sharepoint';
import { BrightcoveApiGateway, BCPlayer, BCVideo, BCGetVideosParams } from '../../services/brightcove';

export class MiniVideos extends React.Component<MiniVideosProps, MiniVideosState> {
    constructor(props: MiniVideosProps, context?: any) {
        super(props, context);

        let searchParameters = getDefaultVideoSearchParameters();
        searchParameters.searchTarget = SearchTarget.VideoCloud;

        this.state = {
            playerFilter: '',
            playerEmbedCode: getDefaultPlayerEmbedCodeProps(),
            searchParameters,
            isLoadingVideos: false,
            videos: []
        };
    }
    
    @autobind
    async componentDidUpdate(prevProps: MiniVideosProps, prevState: MiniVideosState) {
        const { selectedAccount, searchParameters } = this.state;

        if (selectedAccount !== prevState.selectedAccount) {
            await this.searchForVideos(searchParameters);
        }
    }

    @autobind
    async searchForVideos(searchParams: Partial<VideoSearchParameters>): Promise<void> {
        const { selectedAccount } = this.state;
        if (!selectedAccount || !selectedAccount.accountId) {
            return;
        }

        this.setState({
            isLoadingVideos: true
        });

        const { bcApiGateway } = this.props;
        const videosApi = bcApiGateway.videosApi;

        // Map search params to 'q'
        let bcParams: BCGetVideosParams = {
            limit: searchParams.searchLimit || 45,
            sort: searchParams.sortOrder === SortOrder.Descending ? '-' : '',
            q: searchParams.searchTerms ? `+"${searchParams.searchTerms}"` : ''
        };

        switch (searchParams.sortField) {
            case SortField.Name:
                bcParams.sort += 'name';
                break;
            case SortField.Relevance:
                bcParams.sort += 'plays_total';
                break;
            case SortField.CreationDate:
                bcParams.sort += 'created_at';
                break;
            case SortField.StartDate:
                bcParams.sort += 'schedule_starts_at';
                break;
            default:
                // Do nothing.
                break;
        }

        const videos = await videosApi.getVideos(selectedAccount.accountId, bcParams);

        this.setState({
            isLoadingVideos: false,
            videos
        });
    }

    @autobind
    handleAccountChange(selectedAccount: Account) {
        this.setState({
            selectedAccount
        });
    }

    @autobind
    handlePlayerFilterChange(value: string) {
        this.setState({
            playerFilter: value
        });
    }

    @autobind
    handlePlayerChange(selectedPlayer: BCPlayer) {
        this.setState({
            selectedPlayer
        });
    }

    @autobind
    handlePlayerEmbedCodeChanged(playerEmbedCode: Partial<PlayerEmbedCodeProps>) {
        this.setState({
            playerEmbedCode
        });
    }

    @autobind
    handleSearchParametersChanged(searchParameters: Partial<VideoSearchParameters>, fieldChanged: string) {
        this.setState({
            searchParameters
        });

        if (fieldChanged !== 'searchTerms') {
            this.searchForVideos(searchParameters);
        }
    }

    @autobind
    videoSelected(video: BCVideo, isSelected: boolean, e: any) {
        let selectedVideo: BCVideo | undefined;
        if (isSelected) {
            selectedVideo = video;
        }
        this.setState({
            selectedVideo
        });
    }

    @autobind
    renderVideoNameCell(cell: any, video: BCVideo, extraData: { selectedAccount: Account }) {
        return (
            <div className="row">
                {video.images && video.images.thumbnail &&
                    <img className="col-sm-2" src={video.images.thumbnail.src} />
                }
                <div className="col-sm-10">
                    <a href="#">{video.name}</a>
                    <div className="small">{video.id}</div>
                </div>
            </div>
        );
    }

    renderVideoEmbed(selectedAccount: Account, selectedPlayer: BCPlayer, selectedVideo: BCVideo, playerEmbedCodeProps: Partial<PlayerEmbedCodeProps>) {

        const playerPreviewUrl = `https://players.brightcove.net/${selectedAccount.accountId}/${selectedPlayer.id}_default/index.html?videoId=${selectedVideo.id}`;
        const embedCode = ReactDOMServer.renderToStaticMarkup(<PlayerEmbedCode accountId={selectedAccount.accountId} playerId={selectedPlayer && selectedPlayer.id} mode="Video" modeId={selectedVideo.id} {...playerEmbedCodeProps as PlayerEmbedCodeProps} />);

        const labelClass = 'col-md-3 control-label small';
        const controlClass = 'col-md-9';

        return (
            <>
                <div className="form-group">
                    <label className={labelClass}>Preview in browser</label>
                    <div className={controlClass + ' word-wrap'}>
                        <a href={playerPreviewUrl} target="_blank">{playerPreviewUrl}</a>
                    </div>
                </div>
                <div className="col-md-12">
                    <textarea style={{ width: '100%', height: '300px' }} value={embedCode} onChange={() => null} />
                </div>
            </>
        );

    }

    render() {
        const { availableAccounts, bcApiGateway } = this.props;
        const {
            selectedAccount,
            selectedPlayer,
            playerFilter,
            playerEmbedCode,
            searchParameters,
            isLoadingVideos,
            videos,
            selectedVideo
        } = this.state;

        const labelClass = 'col-md-3 control-label small';
        const controlClass = 'col-md-9';

        const selectRow: SelectRow = {
            mode: 'radio',
            clickToSelect: true,
            className: 'info',
            onSelect: this.videoSelected
        };

        return (
            <div className="form-horizontal">
                <div className="form-group">
                    <label className={labelClass}>Brightcove Account:</label>
                    <div className={controlClass}>
                        <AccountPicker className="input-sm" availableAccounts={availableAccounts} selectedAccount={selectedAccount} onChange={this.handleAccountChange} />
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Player Filter:</label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <input
                                type="search"
                                placeholder="Search"
                                className="form-control input-sm"
                                value={playerFilter}
                                onChange={(e) => this.handlePlayerFilterChange(e.target.value)}
                            />
                            <div className="input-group-btn searchButton">
                                <Button bsSize="small"><i className="glyphicon glyphicon-search" /></Button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Player:</label>
                    <div className={controlClass}>
                        <PlayerPicker className="input-sm" bcApiGateway={bcApiGateway} accountId={selectedAccount && selectedAccount.accountId} playerFilter={playerFilter} selectedPlayerId={selectedPlayer && selectedPlayer.id} onChange={this.handlePlayerChange} />
                    </div>
                </div>
                <PlayerEmbedCodeControls
                    labelClass={labelClass}
                    controlClass={controlClass}
                    formControlClass="input-sm"
                    playerEmbedCode={playerEmbedCode}
                    onPlayerEmbedCodeChanged={this.handlePlayerEmbedCodeChanged}
                />
                <VideoSearchControls
                    labelClass={labelClass}
                    controlClass={controlClass}
                    showSearchTarget={false}
                    formControlClass="input-sm"
                    searchParameters={searchParameters}
                    onSearchParametersChanged={this.handleSearchParametersChanged}
                    onSearchForVideos={this.searchForVideos}
                />
                {isLoadingVideos && selectedAccount &&
                    <Spinner loadingText="Loading Videos..." />
                }
                {!isLoadingVideos && selectedAccount &&
                    <div className="well" style={{ height: '250px', overflow: 'auto', margin: '0 15px' }} >
                        <BootstrapTable data={videos} striped={true} bordered={false} selectRow={selectRow}>
                            <TableHeaderColumn dataField="name" dataFormat={this.renderVideoNameCell} formatExtraData={{ selectedAccount }}>Videos</TableHeaderColumn>
                            <TableHeaderColumn dataField="id" isKey={true} hidden={true}>ID</TableHeaderColumn>
                        </BootstrapTable>
                        <hr />
                    </div>
                }
                {selectedAccount && selectedPlayer && selectedVideo &&
                    this.renderVideoEmbed(selectedAccount, selectedPlayer, selectedVideo, playerEmbedCode)
                }
            </div>
        );
    }
}

interface MiniVideosProps {
    availableAccounts: Array<Account>;
    bcApiGateway: BrightcoveApiGateway;
}

interface MiniVideosState {
    selectedAccount?: Account;
    selectedPlayer?: BCPlayer;
    playerFilter: string;
    playerEmbedCode: Partial<PlayerEmbedCodeProps>;
    searchParameters: Partial<VideoSearchParameters>;

    isLoadingVideos: boolean;
    videos: Array<BCVideo>;
    selectedVideo?: BCVideo;
}


