import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';
import { Prompt } from 'react-router';
import { AccountPicker, DatePicker, Spinner } from '../common';
import { EditVideoTextTracks, ActiveTextTrack } from './';
import autobind from 'autobind-decorator';

import { BrightcoveApiGateway, BCVideo, BCCustomField } from '../../services/brightcove';
import { SPListGateway, Account } from '../../services/sharepoint';
import { ConnectorContext } from '../../Common';

import * as moment from 'moment';
import { filter, set, cloneDeep, isEqual } from 'lodash';
import { mapBCVideoToMVideo, mapMVideoToBCVideo, mapMVideoToSPVideo, MVideo } from './mVideo';

import './EditVideo.css';

export class EditVideo extends React.Component<EditVideoProps, EditVideoState> {
    constructor(props: EditVideoProps, context?: any) {
        super(props, context);

        this.state = {
            isLoadingVideo: true,
            isSavingVideo: false,
            isRemovingFromSharePoint: false,
            showRemoveFromSharePointModal: false,
        };
    }

    @autobind
    async componentDidMount() {
        await this.getVideo();
    }

    @autobind
    async getVideo() {
        const { videoLocation, videoId } = this.props;

        if (!videoId) {
            window.location.hash = '#/manage-videos';
            return;
        }

        this.setState({
            isLoadingVideo: true,
        });

        let videoInfo: { bcVideo: BCVideo, video: MVideo, customFields: Array<BCCustomField> };
        switch (videoLocation) {
            case 'SharePoint':
                videoInfo = await this.getSharePointVideo(videoId);
                break;
            case 'Brightcove':
                videoInfo = await this.getBrightcoveVideo(videoId);
                break;
            default:
                window.location.hash = '#/manage-videos';
                return;
        }

        this.setState({
            isLoadingVideo: false,
            video: cloneDeep(videoInfo.video),
            originalVideo: videoInfo.video,
            bcVideo: videoInfo.bcVideo,
            customFields: videoInfo.customFields
        });
    }

    @autobind
    async getSharePointVideo(videoId: string): Promise<{ bcVideo: BCVideo, video: MVideo, customFields: Array<BCCustomField> }> {

        const { spListGateway } = this.props;
        const spVideo = await spListGateway.videosList.getVideoByVideoId(videoId);

        if (!spVideo) {
            throw new Error(`A Video with the specified id could not be found: ${videoId}`);
        }

        // Lols.
        const bcVideoInfo = await this.getBrightcoveVideo(spVideo.referenceId);
        bcVideoInfo.video.sharepointId = spVideo.spid;

        return bcVideoInfo;
    }

    @autobind
    async getBrightcoveVideo(videoId: string): Promise<{ bcVideo: BCVideo, video: MVideo, customFields: Array<BCCustomField> }> {

        const { accountId, bcApiGateway } = this.props;
        const videosApi = bcApiGateway.videosApi;
        const bcVideo = await videosApi.getVideoById(accountId, videoId);

        const video = mapBCVideoToMVideo(bcVideo);

        // Get custom field definitions too
        const customFields = await videosApi.getCustomFields(accountId);

        return {
            bcVideo,
            video,
            customFields: customFields.custom_fields
        };
    }

    @autobind
    addVideoTextTrack(textTrack: ActiveTextTrack) {
        const { video } = this.state;

        if (!video) {
            return;
        }

        if (!video.textTracks) {
            video.textTracks = [];
        }

        video.textTracks.push(textTrack);
        this.setState({
            video
        });
    }

    @autobind
    removeVideoTextTrack(textTrack: ActiveTextTrack) {
        const { video } = this.state;

        if (!video) {
            return;
        }

        if (video.textTracks) {
            const ix = video.textTracks.indexOf(textTrack);
            if (ix < 0) {
                return;
            }

            video.textTracks.splice(ix, 1);
            this.setState({
                video
            });
        }
    }

    @autobind
    async saveToSharePoint() {
        const { accountId, videoId, spListGateway, bcApiGateway, connectorContext } = this.props;
        const { video, selectedAccount } = this.state;

        if (!videoId || !video || !connectorContext.addinWebId || !selectedAccount) {
            return;
        }

        this.setState({
            isSavingVideo: true
        });

        const spVideo = mapMVideoToSPVideo(selectedAccount.title, video);

        const existingSPVideo = await spListGateway.videosList.getVideoByVideoId(videoId);
        if (!existingSPVideo) {
            const newItem = await spListGateway.videosList.createNewItem();
            await spListGateway.videosList.updateItem(newItem.id, spVideo);
        } else {
            await spListGateway.videosList.updateItem(existingSPVideo.id, spVideo);
        }

        // Update the brightcove video with the sharepoint tags.
        const bcVideo = await bcApiGateway.videosApi.getVideoById(accountId, videoId);
        if (!bcVideo.tags) {
            bcVideo.tags = [];
        }

        if (bcVideo.tags.indexOf(video.sharepointId) < 0) {
            bcVideo.tags.push(video.sharepointId);
        }

        const siteTag = `bcsp-${connectorContext.addinWebId.replace(/-/g, '')}-1`;

        if (bcVideo.tags.indexOf(siteTag) < 0) {
            bcVideo.tags.push(siteTag);
        }

        bcApiGateway.videosApi.updateVideo(accountId, videoId, {
            tags: bcVideo.tags
        });

        this.setState({
            isSavingVideo: false
        });

        window.location.hash = '#/manage-videos';
    }

    @autobind
    removeFromSharePoint() {
        this.setState({
            showRemoveFromSharePointModal: true
        });
    }

    @autobind
    removeFromSharePointCancel() {
        this.setState({
            showRemoveFromSharePointModal: false
        });
    }

    @autobind
    async removeFromSharePointConfirm() {
        const { accountId, videoId, bcApiGateway, spListGateway } = this.props;

        if (!accountId || !videoId) {
            return;
        }

        const spVideo = await spListGateway.videosList.getVideoByVideoId(videoId);
        if (spVideo) {
            await spListGateway.videosList.deleteItem(spVideo.id);
        }

        // Remove the SharePoint tags from the BC video.
        const bcVideo = await bcApiGateway.videosApi.getVideoById(accountId, videoId);
        const tags = filter(bcVideo.tags, (t) => !t.startsWith('bcsp-'));
        bcApiGateway.videosApi.updateVideo(accountId, videoId, {
            tags
        });

        window.location.hash = '#/manage-videos';
    }

    @autobind
    async updateVideo() {
        const { accountId, videoId, bcApiGateway, videoLocation } = this.props;
        const { video, bcVideo } = this.state;

        if (!video || !bcVideo || !accountId || !videoId) {
            return;
        }

        this.setState({
            isSavingVideo: true
        });

        const target = mapMVideoToBCVideo(video, filter(bcVideo.tags, (t) => t.startsWith('bcsp-')));

        const videosApi = bcApiGateway.videosApi;
        const bcUpdatedVideo = await videosApi.updateVideo(accountId, videoId, target);

        const updatedVideo = mapBCVideoToMVideo(bcUpdatedVideo);

        // Perform the secondary step to add new text tracks via the injest api.
        // See: https://support.brightcove.com/using-cms-api-add-webvtt-captions#replaceTracks
        const newTextTracks = filter(video.textTracks, (tt) => !tt.id);
        // For each new text track that has a 'file' property, upload to ingest location.
        const newFileTextTracks = filter(newTextTracks as Array<ActiveTextTrack>, (tt) => tt.file);
        if (newFileTextTracks.length > 0) {
            const dynamicIngestApi = bcApiGateway.dynamicIngestApi;

            for (let newFileTextTrack of newFileTextTracks) {
                if (newFileTextTrack.file) {

                    const uploadUrls = await dynamicIngestApi.getUploadUrls(accountId, videoId);
                    await fetch(uploadUrls.signed_url, {
                        method: 'PUT',
                        credentials: 'omit',
                        headers: {
                            'Content-Type': ''
                        },
                        body: newFileTextTrack.file
                    });

                    newFileTextTrack.src = uploadUrls.api_request_url;
                }
            }
        }

        if (newTextTracks.length > 0) {
            await bcApiGateway.dynamicIngestApi.addTextTracks(accountId, videoId, newTextTracks);
        }

        // If we're in 'SharePoint mode' update the sharepoint video too.
        if (videoLocation === 'SharePoint') {
            const { spListGateway } = this.props;
            const { selectedAccount } = this.state;

            if (selectedAccount) {
                const spVideo = mapMVideoToSPVideo(selectedAccount.title, video);

                const existingSPVideo = await spListGateway.videosList.getVideoByVideoId(videoId);

                if (existingSPVideo) {
                    await spListGateway.videosList.updateItem(existingSPVideo.id, spVideo);
                }
            }
        }

        this.setState({
            isSavingVideo: false,
            video: cloneDeep(updatedVideo),
            originalVideo: updatedVideo,
            bcVideo: bcUpdatedVideo
        });
    }

    @autobind
    async handleAccountChange(selectedAccount: Account) {
        this.setState({
            selectedAccount
        });
    }

    @autobind
    handleSelectChange(event: React.ChangeEvent<HTMLSelectElement>) {
        const video = this.state.video;

        if (!video) {
            return;
        }

        const target = event.target;
        const { value, name } = target;

        set(video, name, value);

        this.setState({
            video
        });
    }

    @autobind
    handleDateChange(name: string, value: string | React.ChangeEvent<any> | moment.Moment) {
        const video = this.state.video;

        if (!video) {
            return;
        }

        const momentValue = value as moment.Moment;
        if (momentValue) {
            set(video, name, momentValue.toDate());
        } else {
            set(video, name, null);
        }

        this.setState({
            video
        });
    }

    @autobind
    handleTextAreaChange(event: React.ChangeEvent<HTMLTextAreaElement>) {
        const video = this.state.video;

        if (!video) {
            return;
        }

        const target = event.target;
        const { value, name } = target;

        set(video, name, value);

        this.setState({
            video
        });
    }

    @autobind
    handleInputChange(event: React.ChangeEvent<HTMLInputElement>) {
        const video = this.state.video;

        if (!video) {
            return;
        }

        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        switch (name) {
            case 'tags':
                video.tags = value.toString().split(',');
                video.tags.push(video.sharepointId);
                break;
            default:
                set(video, name, value);
                break;
        }

        this.setState({
            video
        });
    }

    render() {
        const { connectorContext, videoId, accountId, spListGateway, videoLocation } = this.props;
        const {
            selectedAccount,
            isLoadingVideo,
            isSavingVideo,
            video,
            originalVideo,
            customFields,
        } = this.state;

        if (isSavingVideo) {
            return (
                <Spinner loadingText="Saving Video..." />
            );
        }

        if (isLoadingVideo || !video || !originalVideo) {
            return (
                <Spinner loadingText="Loading Video..." />
            );
        }

        const labelClass = 'col-md-3 control-label';
        const controlClass = 'col-md-9';

        const isSaveDisabled = !!videoId && isEqual(video, originalVideo);

        return (
            <>
                <ol className="breadcrumb">
                    <li><a href="#/">Brightcove Management</a></li>
                    <li><a href="#/manage-videos">Manage Videos</a></li>
                    <li><a href="#/manage-videos">Account {accountId}</a></li>
                    <li className="active">Video {videoId}</li>
                </ol>
                <div className="form-horizontal">

                    <div className="form-group">
                        <label className={labelClass}>Select Account</label>{' '}
                        <div className={controlClass}>
                            <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccount={selectedAccount} onChange={this.handleAccountChange} disabled={true} />
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}>Reference ID</label>
                        <div className={controlClass}>
                            <input className="form-control" disabled={true} value={video.referenceId} />
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}>SharePoint ID</label>
                        <div className={controlClass}>
                            <input className="form-control" disabled={true} value={video.sharepointId} />
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={`${labelClass} required`}>Name</label>
                        <div className={controlClass}>
                            <input
                                className="form-control validate"
                                type="text"
                                placeholder="The name of the video"
                                name="name"
                                value={video.name}
                                onChange={this.handleInputChange}
                            />
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}>Short Description</label>
                        <div className={controlClass}>
                            <input
                                className="form-control"
                                type="text"
                                placeholder="Short description here..."
                                name="shortDescription"
                                value={video.shortDescription}
                                onChange={this.handleInputChange}
                            />
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}>Long Description</label>
                        <div className={controlClass}>
                            <textarea
                                className="form-control"
                                placeholder="Long description here..."
                                name="longDescription"
                                value={video.longDescription}
                                onChange={this.handleTextAreaChange}
                            />
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}>Is Active</label>
                        <div className={controlClass}>
                            <select className="form-control" name="state" value={video.state} onChange={this.handleSelectChange}>
                                <option value="ACTIVE">Active</option>
                                <option value="INACTIVE">Inactive</option>
                            </select>
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}>Related Link URL</label>
                        <div className={controlClass}>
                            <input className="form-control" type="text" name="relatedLinkUrl" value={video.relatedLinkUrl} onChange={this.handleInputChange} />
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}>Related Link Text</label>
                        <div className={controlClass}>
                            <input className="form-control" type="text" name="relatedLinkText" value={video.relatedLinkText} onChange={this.handleInputChange} />
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}>Start Availability Date</label>
                        <div className={controlClass}>
                            <DatePicker value={video.startAvailabilityDate} name="startAvailabilityDate" onChange={(e) => this.handleDateChange('startAvailabilityDate', e)} />
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}>End Availability Date</label>
                        <div className={controlClass}>
                            <DatePicker value={video.endAvailabilityDate} name="endAvailabilityDate" onChange={this.handleInputChange} />
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}>Economics</label>
                        <div className={controlClass}>
                            <select className="form-control" name="economics" value={video.economics} onChange={this.handleSelectChange}>
                                <option value="FREE">Free</option>
                                <option value="AD_SUPPORTED">Ad Supported</option>
                            </select>
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}> Brightcove Tags (comma-separated)</label>
                        <div className={controlClass}>
                            <input className="form-control" value={video.tags.join(',')} name="tags" onChange={this.handleInputChange} />
                        </div>
                    </div>
                </div>

                <br />
                {customFields &&
                    <>
                        <h4><strong>Custom Fields</strong></h4>
                        <hr />
                        <div className="form-horizontal">
                            {customFields.map((customField) =>
                                <div className="form-group" key={customField.id}>
                                    <label className={`${labelClass}${customField.required ? ' required' : ''}`}>{customField.display_name}</label>
                                    <div className={controlClass}>
                                        {customField.type === 'string' &&
                                            <input
                                                className="form-control"
                                                required={customField.required}
                                                value={video.customFields[customField.id] || ''}
                                                name={`customFields.${customField.id}`}
                                                onChange={this.handleInputChange}
                                            />
                                        }
                                        {customField.type === 'enum' &&
                                            <select
                                                className="form-control"
                                                required={customField.required}
                                                value={video.customFields[customField.id] || ''}
                                                name={`customFields.${customField.id}`}
                                                onChange={this.handleSelectChange}
                                            >
                                                <option value="" />
                                                {customField.enum_values.map((possibleValue, i) =>
                                                    <option key={i}>{possibleValue}</option>
                                                )}
                                            </select>
                                        }
                                        <small className="form-text text-muted">{customField.description}</small>
                                    </div>
                                </div>
                            )}
                        </div>
                        <br />
                    </>
                }

                <EditVideoTextTracks
                    video={video}
                    onAddTextTrack={this.addVideoTextTrack}
                    onRemoveTextTrack={this.removeVideoTextTrack}
                />

                <h4><strong>Image Details</strong></h4>
                <hr />
                <div className="form-horizontal">
                    <div className="form-group">
                        <label className={labelClass}>Video Still</label>
                        <div className={controlClass}>
                            <img className="col-md-12" src={video.videoStillUrl} />
                        </div>
                    </div>

                    <div className="form-group">
                        <label className={labelClass}>Thumbnail</label>
                        <div className={controlClass}>
                            <img style={{ paddingLeft: '15px', paddingRight: '15px' }} src={video.thumbnailUrl} />
                        </div>
                    </div>
                </div>

                <hr />
                <div>
                    <Button
                        className="btn-space"
                        bsStyle={!isSaveDisabled ? 'primary' : 'default'}
                        disabled={isSaveDisabled}
                        onClick={this.updateVideo}
                    >
                        Save
                    </Button>
                    {videoLocation === 'SharePoint' &&
                        <Button
                            className="btn-space"
                            onClick={this.saveToSharePoint}
                        >
                            Overwrite with Video Cloud Updates
                        </Button>
                    }
                    {videoLocation === 'Brightcove' &&
                        <Button
                            className="btn-space"
                            bsStyle={'default'}
                            onClick={this.saveToSharePoint}
                        >
                            Save to SharePoint
                        </Button>
                    }

                    {videoLocation === 'SharePoint' &&
                        <Button
                            className="btn-space"
                            bsStyle={'warning'}
                            onClick={this.removeFromSharePoint}
                        >
                            Remove from SharePoint
                        </Button>
                    }
                </div>
                <Modal show={this.state.showRemoveFromSharePointModal} onHide={this.removeFromSharePointCancel}>
                    <Modal.Header closeButton={true}>
                        <Modal.Title>Confirm Removal from SharePoint</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <h5>This video will be removed from SharePoint, but will be still contained in the Brightcove Video Cloud.</h5>
                        <h5> Are you sure you want to proceed?</h5>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={this.removeFromSharePointConfirm} bsStyle="danger">Remove Video from SharePoint</Button>
                        <Button onClick={this.removeFromSharePointCancel}>Cancel</Button>
                    </Modal.Footer>
                </Modal>
                <Prompt
                    when={!isSaveDisabled}
                    message="There are unsaved changes. Are you sure you wish to leave?"
                />
            </>
        );
    }
}

interface EditVideoProps {
    connectorContext: ConnectorContext;
    bcApiGateway: BrightcoveApiGateway;
    spListGateway: SPListGateway;
    accountId: string;
    videoLocation: 'SharePoint' | 'Brightcove';
    videoId?: string;
}

interface EditVideoState {
    selectedAccount?: Account;
    isLoadingVideo: boolean;
    isSavingVideo: boolean;

    isRemovingFromSharePoint: boolean;
    showRemoveFromSharePointModal: boolean;

    video?: MVideo;
    originalVideo?: MVideo;
    bcVideo?: BCVideo;
    customFields?: Array<BCCustomField>;
}


