import { BCAccessTokenService, BCGetAllPlayersResponse } from './index';
import { PlayerManagementEndpointUrl } from './Endpoints';
/**
 * See:
 * https://brightcovelearning.github.io/Brightcove-API-References/player-management-api/v2/doc/index.html#api-Players-Get_All_Players
 */
export class PlayerManagementApi {
    private readonly _accessTokenService: BCAccessTokenService;

    constructor(accessTokenService: BCAccessTokenService) {
        this._accessTokenService = accessTokenService;
    }

    public async getAllPlayers(accountId: string): Promise<BCGetAllPlayersResponse> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let response = await fetch(`${PlayerManagementEndpointUrl}/${accountId}/players`, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }

    public async getPlayerEmbeds(accountId: string, playerId: string): Promise<any> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let response = await fetch(`${PlayerManagementEndpointUrl}/${accountId}/players/${playerId}/embeds`, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }
}



