import { BCAccessTokenService, BCGetClientCredentialByIdResponse } from './index';
import { ClientCredentialsEndpointUrl } from './Endpoints';
/**
 * See:
 * https://brightcovelearning.github.io/Brightcove-API-References/oauth-api/v4/doc/index.html#api-Client_Credential
 */
export class ClientCredentialsApi {
    private readonly _accessTokenService: BCAccessTokenService;

    constructor(accessTokenService: BCAccessTokenService) {
        this._accessTokenService = accessTokenService;
    }

    public async getClientCredentialById(accountId: string, clientId: string): Promise<BCGetClientCredentialByIdResponse> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let response = await fetch(`${ClientCredentialsEndpointUrl}/${clientId}`, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }
}



