import {
    BCAccessTokenService,
    BCGetVideoCountResponse,
    BCGetCustomFieldsResponse,
    BCGetVideosParams,
    BCGetVideoCountParams,
    BCVideo,
    BCIngestJob
} from './index';
import { CmsEndpointUrl } from './Endpoints';
import { getQueryString } from '../../services/Utils';

/**
 * See:
 * https://brightcovelearning.github.io/Brightcove-API-References/cms-api/v1/doc/index.html#api-videoGroup-Get_Video_Count
 */

export class VideosApi {
    private readonly _accessTokenService: BCAccessTokenService;

    constructor(accessTokenService: BCAccessTokenService) {
        this._accessTokenService = accessTokenService;
    }

    /**
     * https://brightcovelearning.github.io/Brightcove-API-References/cms-api/v1/doc/index.html#api-videoGroup-Create_Video
     * @param accountId 
     */
    public async createVideo(accountId: string, name: string): Promise<BCVideo> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/videos`;
        let response = await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            },
            body: JSON.stringify({
                name
            })
        });

        return await response.json();
    }

    public async getVideos(accountId: string, options?: BCGetVideosParams): Promise<Array<BCVideo>> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/videos`;
        if (options) {
            url += '?' + getQueryString(options);
        }

        let response = await fetch(url, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }

    public async getVideoById(accountId: string, videoId: string): Promise<BCVideo> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/videos/${videoId}`;
        let response = await fetch(url, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }

    public async updateVideo(accountId: string, videoId: string, video: Partial<BCVideo>): Promise<BCVideo> {

        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/videos/${videoId}`;
        let response = await fetch(url, {
            method: 'PATCH',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            },
            body: JSON.stringify(video)
        });

        return await response.json();

    }

    public async getVideoInjestJobs(accountId: string, videoId: string): Promise<Array<BCIngestJob>> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/videos/${videoId}/ingest_jobs`;

        let response = await fetch(url, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }

    public async getVideoCount(accountId: string, options?: BCGetVideoCountParams): Promise<BCGetVideoCountResponse> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/counts/videos`;
        if (options) {
            url += '?' + getQueryString(options);
        }

        let response = await fetch(url, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();
    }

    /**
     * See: https://brightcovelearning.github.io/Brightcove-API-References/cms-api/v1/doc/index.html#api-videoGroup-Get_Custom_Fields
     * @param accountId 
     */
    public async getCustomFields(accountId: string): Promise<BCGetCustomFieldsResponse> {
        const accessToken = await this._accessTokenService.obtainAccessToken(accountId);

        let url = `${CmsEndpointUrl}/${accountId}/video_fields`;

        let response = await fetch(url, {
            method: 'GET',
            cache: 'no-cache',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `${accessToken.token_type} ${accessToken.access_token}`
            }
        });

        return await response.json();

    }
}


