import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { Spinner } from '../common';

import { BCExperience } from '../../services/brightcove';

export class ExperienceEmbed extends React.Component<ExperienceEmbedProps> {
    constructor(props: ExperienceEmbedProps, context?: any) {
        super(props, context);

        this.state = {
            isLoadingExperience: true
        };
    }

    renderEmbedCode(accountId: string, experience: BCExperience) {

        const scriptUrl = `https://players.brightcove.net/${accountId}/experience_${experience.id}/live.js`;

        const style: React.CSSProperties = {};
        if (experience.width) {
            style.width = `${experience.width}px`;
        }

        if (experience.height) {
            style.height = `${experience.height}px`;
        }

        return (
            <>
                <div data-experience={experience.id} style={style} />
                <script src={scriptUrl} />
            </>
        );
    }

    render() {
        const { accountId, experience, labelClass, controlClass, showDilly } = this.props;

        if (!experience) {
            return (
                <div className="text-center">
                    <i>An experience has not been selected. Please select an experience above to obtain the embed code.</i>
                </div>
            );
        }

        const experienceSharableUrl = `https://players.brightcove.net/${accountId}/experience_${experience.id}/share.html`;
        const embedCode = ReactDOMServer.renderToStaticMarkup(this.renderEmbedCode(accountId, experience));

        let layoutSize = 'Responsive';
        if (experience.width) {
            layoutSize = `Fixed width of ${experience.width}px`;
        }

        if (experience.height) {
            layoutSize += ` and height of ${experience.height}px`;
        }

        return (
            <>
                <div className="form-group">
                    <label className={labelClass}>Experience Name</label>
                    <div className={controlClass}>{experience.name}</div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Shareable URL</label>
                    <div className={controlClass}><a href={experienceSharableUrl} target="_blank">{experienceSharableUrl}</a></div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Layout Size</label>
                    <div className={controlClass}>{layoutSize}</div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Embed Code</label>
                    <div className={controlClass}>
                        {(showDilly === undefined || showDilly === true) &&
                            <div>After your experience has been published once, additional published changes will be included in the existing embed code. You do not need to re-copy and paste the embed code into existing locations.</div>
                        }
                        <textarea style={{ width: '100%', height: '80px' }} value={embedCode} onChange={() => null} />
                    </div>
                </div>
            </>
        );
    }
}

export interface ExperienceEmbedProps {
    accountId: string;
    experience?: BCExperience;
    labelClass?: string;
    controlClass?: string;
    showDilly?: boolean;
}


