import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import { AccountPicker, Spinner } from '../common';
import { ExperienceEmbed } from '../embeds';

import { BrightcoveApiGateway, BCExperience } from '../../services/brightcove';
import { SPListGateway, Account } from '../../services/sharepoint';
import { ConnectorContext } from '../../Common';

import autobind from 'autobind-decorator';
import './ManageExperiences.css';

export class ManageExperiences extends React.Component<ManageExperiencesProps, ManageExperiencesState> {
    constructor(props: ManageExperiencesProps, context?: any) {
        super(props, context);

        this.state = {
            isLoadingExperiences: true,
            showEmbedModal: false
        };
    }

    @autobind
    async handleAccountChange(selectedAccount: Account) {
        this.setState({
            isLoadingExperiences: true,
            selectedAccount
        });

        const experiencesApi = this.props.bcApiGateway.experiencesApi;
        const experiencesResponse = await experiencesApi.getExperiences(selectedAccount.accountId);

        this.setState({
            isLoadingExperiences: false,
            experiences: experiencesResponse.items
        });
    }

    @autobind
    showEmbedModal(exerience: BCExperience) {
        this.setState({
            showEmbedModal: true,
            embedExperience: exerience
        });
    }

    @autobind
    hideEmbedModal() {
        this.setState({
            showEmbedModal: false,
            embedExperience: undefined
        });
    }

    @autobind
    renderExperienceNameCell(cell: any, experience: BCExperience, extraData: { selectedAccount: Account }) {
        const { selectedAccount } = extraData;
        const editPlaylistUrl = `#/manage-experiences/edit/${selectedAccount && selectedAccount.accountId}/experience/${experience.id}`;
        return (
            <>
                <a href={editPlaylistUrl}>{experience.name}</a>
                <br />
                {experience.videos.videoIds && experience.videos.videoIds.length > 0 &&
                    <>
                        <div style={{ fontSize: 'small' }}>Video IDs in list: </div>
                        <div style={{ fontSize: 'xx-small' }}>
                            {experience.videos.videoIds.map((videoId) =>
                                <span key={videoId} style={{ paddingLeft: '5px', float: 'left' }}>{videoId}</span>
                            )}
                        </div>
                    </>
                }
                {(!experience.videos.videoIds || experience.videos.videoIds.length === 0) &&
                    <i>No Videos</i>
                }
            </>
        );
    }

    @autobind
    renderEditLink(cell: any, experience: BCExperience, extraData: { selectedAccount: Account }): React.ReactElement<any> {
        const { selectedAccount } = extraData;
        const editPlaylistUrl = `#/manage-experiences/edit/${selectedAccount && selectedAccount.accountId}/experience/${experience.id}`;

        return (
            <div className="result text-center">
                <div>
                    <Button
                        bsStyle="link"
                        style={{ width: '70px' }}
                        href={editPlaylistUrl}
                    >
                        Edit
                    </Button>
                </div>
                <div>
                    <Button
                        bsStyle="success"
                        style={{ width: '70px' }}
                        bsSize="small"
                        onClick={() => this.showEmbedModal(experience)}
                    >
                        Embed
                    </Button>
                </div>
            </div>
        );
    }

    render() {
        const { bcApiGateway, connectorContext } = this.props;
        const {
            isLoadingExperiences,
            experiences,
            selectedAccount,
            showEmbedModal,
            embedExperience
        } = this.state;

        return (
            <>
                <ol className="breadcrumb">
                    <li><a href="#/">Brightcove Management</a></li>
                    <li className="active">Manage Experiences</li>
                </ol>
                <div className="formContent">
                    <label>Select Account:</label>{' '}
                    <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccount={selectedAccount} onChange={this.handleAccountChange} />
                </div>
                {selectedAccount &&
                    <>
                        {isLoadingExperiences &&
                            <Spinner loadingText="Loading Experiences..." />
                        }
                        {!isLoadingExperiences && experiences &&
                            <>
                                <BootstrapTable data={experiences} striped={true} bordered={false}>
                                    <TableHeaderColumn dataField="name" dataFormat={this.renderExperienceNameCell} formatExtraData={{ selectedAccount }} dataSort={true}>Name</TableHeaderColumn>
                                    <TableHeaderColumn dataField="description" dataSort={true}>Short Description</TableHeaderColumn>
                                    <TableHeaderColumn dataField="template" dataSort={true}>Type</TableHeaderColumn>
                                    <TableHeaderColumn width="200" dataField="id" dataSort={true} isKey={true}>ID</TableHeaderColumn>
                                    <TableHeaderColumn width="90" dataFormat={this.renderEditLink} formatExtraData={{ selectedAccount }}>Edit Link</TableHeaderColumn>
                                </BootstrapTable>
                                <hr />
                            </>
                        }
                        {embedExperience &&
                            <Modal className="embed" show={showEmbedModal} onHide={this.hideEmbedModal} bsSize="large">
                                <Modal.Header closeButton={true}>
                                    <Modal.Title>Embed Video</Modal.Title>
                                </Modal.Header>
                                <Modal.Body>
                                    <div className="form-horizontal">
                                        <ExperienceEmbed
                                            accountId={selectedAccount.accountId}
                                            experience={embedExperience}
                                        />
                                    </div>
                                </Modal.Body>
                                <Modal.Footer />
                            </Modal>
                        }
                    </>
                }
            </>
        );
    }
}

interface ManageExperiencesProps {
    connectorContext: ConnectorContext;
    bcApiGateway: BrightcoveApiGateway;
    spListGateway: SPListGateway;
}

interface ManageExperiencesState {
    isLoadingExperiences: boolean;
    experiences?: Array<BCExperience>;
    selectedAccount?: Account;

    showEmbedModal: boolean;
    embedExperience?: BCExperience;
}



