import * as React from 'react';
import autobind from 'autobind-decorator';

import { PlayerEmbedCodeProps } from './PlayerEmbedCode';

export class PlayerEmbedCodeControls extends React.Component<PlayerEmbedControlsProps> {
    @autobind
    handleAutoplayChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { playerEmbedCode, onPlayerEmbedCodeChanged } = this.props;
        playerEmbedCode.autoplay = event.target.checked;
        onPlayerEmbedCodeChanged(playerEmbedCode);
    }

    @autobind
    handleMutedChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { playerEmbedCode, onPlayerEmbedCodeChanged } = this.props;
        playerEmbedCode.muted = event.target.checked;
        onPlayerEmbedCodeChanged(playerEmbedCode);
    }

    @autobind
    handleSizingChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { playerEmbedCode, onPlayerEmbedCodeChanged } = this.props;
        playerEmbedCode.sizing = event.target.value as 'Responsive' | 'Fixed';
        onPlayerEmbedCodeChanged(playerEmbedCode);
    }

    @autobind
    handleAspectRatioChange(event: React.ChangeEvent<HTMLSelectElement>) {
        const { playerEmbedCode, onPlayerEmbedCodeChanged } = this.props;
        const { playerHeight } = playerEmbedCode;

        playerEmbedCode.aspectRatio = event.target.value as '16:9' | '4:3' | 'Custom';
        playerEmbedCode.playerHeight = playerEmbedCode.aspectRatio !== 'Custom' ? '' : playerHeight;

        onPlayerEmbedCodeChanged(playerEmbedCode);
    }

    @autobind
    handleWidthChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { playerEmbedCode, onPlayerEmbedCodeChanged } = this.props;
        playerEmbedCode.playerWidth = event.target.value || '';

        onPlayerEmbedCodeChanged(playerEmbedCode);
    }

    @autobind
    handleHeightChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { playerEmbedCode, onPlayerEmbedCodeChanged } = this.props;
        playerEmbedCode.playerHeight = event.target.value || '';

        onPlayerEmbedCodeChanged(playerEmbedCode);
    }

    @autobind
    handleUnitsChange(event: React.ChangeEvent<HTMLSelectElement>) {
        const { playerEmbedCode, onPlayerEmbedCodeChanged } = this.props;
        playerEmbedCode.units = event.target.value as 'px' | 'em' | '%';

        onPlayerEmbedCodeChanged(playerEmbedCode);
    }

    render() {
        const { labelClass, controlClass, formControlClass, playerEmbedCode } = this.props;

        return (
            <>
                <div className="form-group">
                    <label className={labelClass}>Options</label>
                    <div className={controlClass}>
                        <div className={`input-group ${formControlClass}`}>
                            <label className="checkbox-inline">
                                <input type="checkbox" checked={playerEmbedCode.autoplay} onChange={this.handleAutoplayChange} />Autoplay
                            </label>
                            <label className="checkbox-inline">
                                <input type="checkbox" checked={playerEmbedCode.muted} onChange={this.handleMutedChange} />Muted
                            </label>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Sizing</label>
                    <div className={controlClass}>
                        <div className={`input-group ${formControlClass}`}>
                            <label className="radio-inline">
                                <input
                                    type="radio"
                                    name="sizing"
                                    value="Responsive"
                                    checked={playerEmbedCode.sizing === 'Responsive'}
                                    onChange={this.handleSizingChange}
                                /> Responsive
                                    </label>
                            <label className="radio-inline">
                                <input
                                    type="radio"
                                    name="sizing"
                                    value="Fixed"
                                    checked={playerEmbedCode.sizing === 'Fixed'}
                                    onChange={this.handleSizingChange}
                                /> Fixed</label>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Aspect Ratio</label>
                    <div className={controlClass}>
                        <select className={`form-control ${formControlClass}`} value={playerEmbedCode.aspectRatio} onChange={this.handleAspectRatioChange}>
                            <option>16:9</option>
                            <option>4:3</option>
                            <option>Custom</option>
                        </select>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>WxH</label>
                    <div className={controlClass}>
                        <div className={`form-group ${formControlClass} col-md-6`}>
                            <label className={labelClass} style={{ paddingTop: '0' }}>Player Width</label>
                            <div className={controlClass}>
                                <input className={`form-control ${formControlClass}`} type="number" placeholder="auto" value={playerEmbedCode.playerWidth} onChange={this.handleWidthChange} />
                            </div>
                        </div>
                        <div className={`form-group ${formControlClass} col-md-6`}>
                            <label className={labelClass} style={{ paddingTop: '0' }}>Player Height</label>
                            <div className={controlClass}>
                                <input className={`form-control ${formControlClass}`} type="number" placeholder="auto" value={playerEmbedCode.playerHeight} onChange={this.handleHeightChange} />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Units</label>
                    <div className={controlClass}>
                        <select className={`form-control ${formControlClass}`} value={playerEmbedCode.units} onChange={this.handleUnitsChange}>
                            <option>px</option>
                            <option>em</option>
                            <option>%</option>
                        </select>
                    </div>
                </div>
            </>
        );
    }
}

export interface PlayerEmbedControlsProps {
    labelClass?: string;
    controlClass?: string;
    formControlClass?: string;
    playerEmbedCode: Partial<PlayerEmbedCodeProps>;
    onPlayerEmbedCodeChanged: (playerEmbedCode: Partial<PlayerEmbedCodeProps>) => void;
}


